@charset "utf-8";

.plum-mypage {
    padding-bottom: 200px;

    .mypage-head {
        height: auto;
        background: $plumcolor;

        .box {
            height: 114px;
        }

        .lnb-wrap {
            position: relative;
            max-width: 1200px;
            margin: 0 auto;

            .lnb {
                @include flexbox;

                li {
                    @include flex(1, 0, auto);
                }
                
                .btn-lnb {
                    display: block;
                    height: 60px;
                    font-size: 20px;

                    @include flexbox;
                    @include align-items(center);
                    @include justify-content(center);

                    &.active {
                        border-bottom: 6px solid #ffc033;
                        color: #ffc033;
                        font-weight: 800;
                    }
                }
            }

            // 모바일 전체보기 버튼
            .btn-lnb-mobile {
                display: none;
                position: absolute;
                right: 5px;
                top: 5px;
                border: 0;
                color: #fff;
                background: #6309bd;

                @include size(30px);
            }
        }

        // 모바일 lnb 전체
        .lnb-mobile {
            display: none;
            position: absolute;
            top: 100%;
            width: 100%;
            border: {
                width: 0 1px;
                style: solid;
                color:#ccc;
            }
            background: #fff;
            z-index: 2;

            @include clear;
            @include box-sizing;
            @include box-shadow(0px 2px 3px 0px rgba(38, 59, 94, 0.1));

            li {
                float: left;
                width: calc(100% / 2);
                border-bottom: 1px solid #ccc;

                // @include flex;
                @include box-sizing;
                
                &:nth-child(odd) {
                    border-right: 1px solid #ccc;
                }

                .active {
                    font-weight: 800;
                    background: #f9f9f9;
                }
            }

            a {
                display: block;
                padding: 12px;
                font-size: 15px;
                color: #222;
            }

            &:after {
                content: "";
                position: absolute;
                left: 0;
                bottom: 0;
                width: 100%;
                height: 1px;
                font-size: 0;
                background: #ccc;
                line-height: 0;
            }

            &.on {
                display: block;
            }
        }
    }

    // 요약
    .section-dashboard {
        .status-wrap {
            @include default-width(1200px);
        }

        .status-items {
            margin-top: 120px;

            @include clear;

            .item {
                position: relative;
                float: left;
                width: calc((100% - 60px) / 2);
                min-height: 260px;
                margin-left: 60px;

                .h2-tit {
                    height: 60px;
                    border-bottom: 2px solid $basecolor;
                    font-size: 32px;
                    font-weight: 800;
                }

                .box {
                    margin-top: 60px;
                }

                .summary {
                    @include clear;

                    li {
                        float: left;
                        width: calc(100% / 3);
                        font-size: 24px;
                        font-weight: 800;
                        text-align: center;
                    }

                    .cnt {
                        display: block;
                        margin-bottom: 10px;
                        font-size: 80px;
                    }
                }
                
                .list {
                    li {
                        position: relative;
                        padding: 15px 0 15px 20px;
                        border-top: 1px solid #dfefee;
                        font-size: 16px;
                        font-weight: 700;

                        @include flexbox;
                        @include justify-content(space-between);

                        .conts {
                            font-weight: 800;
                        }

                        .btn-lnk {
                            color: $plumcolor;
                            text-decoration: underline;
                        }

                        &:before {
                            content: "";
                            position: absolute;
                            left: 0;
                            top: 50%;
                            margin-top: -2px;
                            background: $basecolor;

                            @include size(4px);
                            @include border-radius(100px);
                        }

                        &:first-child {
                            border-top: 0;
                        }
                    }

                    &.total {
                        margin-top: 40px;
                    }

                    &.noti {
                        .cnt {
                            color: $plumcolor;
                        }
                    }

                    & + .list {
                        li {
                            &:first-child {
                                border-top: 1px solid #dfefee;
                            }
                        }
                    }
                }

                .btn-more {
                    position: absolute;
                    right: 0;
                    top: 0;
                    font-size: 24px;
                    font-weight: 700;
                    line-height: 32px;
                }

                .nodata {
                    text-align: center;

                    strong {
                        display: block;
                        font-size: 32px;
                    }

                    .lnk {
                        display: inline-block;
                        margin-top: 15px;
                        font-size: 20px;
                        color: $plumcolor;
                        text-decoration: underline;
                        vertical-align: top;
                    }
                }

                &:nth-child(odd) {
                    margin-left: 0;
                }
            }

            &:first-child {
                margin-top: 0;
            }
        }
    }

    // 투자
    .section-invest {
        // 진행중
        .invest-ready {
            .funding-graph {
                max-width: 1200px;
                margin: 0 auto;
            }
    
            .invest-product {
                margin-top: 60px;
    
                @include default-width(1200px);
                @include clear;
    
                .box {
                    float: left;
                    width: calc((100% - 60px) / 2);
                    margin-left: 60px;
    
                    .h2-tit {
                        position: relative;
                        height: 60px;
                        border-bottom: 2px solid $basecolor;
                        font-size: 32px;
                        font-weight: 800;
    
                        .addinfo {
                            position: absolute;
                            right: 0;
                            top: 0;
                            font-size: 24px;
                            font-weight: 700;
                            line-height: 32px;
                        }
                    }
    
                    .product-info {
                        margin-top: 15px;
                    }
    
                    &:first-child {
                        margin-left: 0;
                    }
                }
            }
        }

        // 완료
        .invest-detail {
            @include default-width(1200px);

            .detail-top {
                position: relative;
                height: 60px;
                margin-bottom: 60px;
                border-bottom: 2px solid $basecolor;

                .h2-tit {
                    font-size: 32px;
                    font-weight: 800;
                }

                .purpose {
                    position: absolute;
                    right: 0;
                    top: 0;
                    font-size: 24px;
                    font-weight: 700;
                    line-height: 32px;
                }
            }

            .detail-info {
                margin-top: 75px;

                .invest-item {
                    display: none;

                    &.active {
                        display: block;
                    }
                }

                .invest-state {
                    @include clear;
                }

                .current-revenue {
                    float: right;
                    width: calc((100% / 3) - 30px);
                    font-size: 32px;
                    line-height: 1.5;
                    
                    strong {
                        display: block;

                        span {
                            border-bottom: 1px solid $plumcolor;
                            color: $plumcolor;
                        }
                    }
                }

                .product-info {
                    float: left;
                    width: calc((100% / 3) * 2 - 30px);
                    margin-right: 60px;

                    .info-items {
                        position: relative;
                    }
                }

                .btn-payments-schedule {
                    position: absolute;
                    right: 0;
                    top: -6px;
                    width: auto;
                    height: 32px;
                    padding: 0 12px;
                    font-size: 14px;
                    color: $basecolor1;
                }

                .invest-info {
                    & + .invest-info {
                        margin-top: 80px;
                        padding-top: 75px;
                        border-top: 1px solid #ccc;
                    }
                }
            }

            // 예상 지급 스케줄
            .payments-schedule {
                margin-top: 40px;
            }

            .invest-bottom {
                margin-top: 80px;
                text-align: center;
            }
        }

        // 하단 공통
        .invest-bottom {
            margin-top: 40px;
    
            @include default-width(1200px);

            .button-group {
                padding-top: 40px;
                border-top: 1px solid #ededed;

                @include flexbox;
                @include justify-content(space-between);
                @include flex-direction(row-reverse);
                // @include flex-wrap();

                &.split {
                    .col {
                        @include flex;

                        .btn {
                            margin-left: 10px;

                            &:first-child {
                                margin-left: 0;
                            }
                        }
        
                        &:first-child {
                            text-align: right;
                        }
                    }
                }
            }
        }

        // 자동 투자 리스트
        .invest-list {
            @include default-width(1200px);

            .list-top {
                position: relative;
                height: 60px;
                margin-bottom: 60px;
                border-bottom: 2px solid $basecolor;

                .h2-tit {
                    font-size: 32px;
                    font-weight: 800;

                    .cnt {
                        color: $plumcolor;
                    }
                }

                .top-button {
                    position: absolute;
                    right: 0;
                    top: 0;
                }
            }

            .paging {
                margin-top: 25px;
            }
        }
    }

    // 대출
    .section-loan {
        .loan-state {
            @include default-width(1200px);
            
            .state-top {
                position: relative;
                height: 60px;
                margin-bottom: 15px;
                border-bottom: 2px solid $basecolor;

                .h2-tit {
                    font-size: 32px;
                    font-weight: 800;
                }

                .purpose {
                    position: absolute;
                    right: 0;
                    top: 0;
                    font-size: 24px;
                    font-weight: 700;
                    line-height: 32px;
                }
            }

            .state-info {
                @include clear;
            }

            .state-noti {
                // overflow: hidden;
                float: right;
                width: calc((100% / 3) - 30px);
                padding-top: 15px;

                .state-tit {
                    font-size: 32px;
                    font-weight: 800;
                }

                .state-reason {
                    margin-top: 30px;
                    font-size: 24px;
                }

                .state-dsc {
                    margin-top: 15px;
                    font-size: 24px;
                    line-height: 1.3;
                }
            }

            .loan-set {
                margin-top: 50px;

                .loan-partial-set {
                    position: relative;

                    .loan-partial-tit {
                        position: relative;

                        .tit {
                            line-height: 32px;
                        }
                    }

                    .icheck-switch {
                        position: absolute;
                        right: 0;
                        top: 0;
                    }
                }

                .tit {
                    padding-left: 20px;

                    &:before {
                        content: "";
                        position: absolute;
                        left: 0;
                        top: 50%;
                        border-radius: 100px;
                        background: #222;

                        @include size(4px);
                        @include translate(0, -50%);
                    }
                }

                .loan-partial-dsc {
                    margin-top: 20px;
                    padding-left: 17px;
                    font-size: 14px;
                    color: #666;
                    text-indent: -17px;
                    line-height: 1.3;

                    strong {
                        color: $plumcolor;
                    }
                }

                .loan-funding {
                    position: relative;
                    margin-top: 10px;
                    padding-top: 10px;
                    border-top: 1px solid #ededed;

                    .tit {
                        position: relative;
                        display: block;
                        line-height: 48px;
                    }

                    .loan-period {
                        position: absolute;
                        right: 0;
                        top:10px ;
                        font-size: 0;
                    }

                    .slt {
                        height: 48px;
                        border-bottom: 4px solid #222;
                        font-size: 16px;
                    }

                    .istyle {
                        width: 100px;
                        height: 48px;
                        margin-left: 5px;
                        border-bottom: 4px solid #222;
                        font-size: 16px;
                        text-align: center;
                    }
                }

                .loan-dsc {
                    margin-top: 20px;
                    padding-left: 10px;
                    font-size: 14px;
                    color: #666;
                    line-height: 1.4;
                    text-indent: -11px;
                }
            }

            .product-info {
                float: left;
                width: calc((100% / 3) * 2 - 30px);
                margin-right: 60px;
            }

            .required-notice {
                margin-top: 80px;
            }

            .funding-graph {
                margin-bottom: 50px;
            }
        }

        .loan-bottom {
            margin-top: 40px;

            .button-group {
                padding-top: 40px;
                border-top: 1px solid #ededed;

                @include flexbox;
                @include justify-content(space-between);
                @include flex-direction(row-reverse);
                // @include flex-wrap();

                .btn {
                    margin-left: 10px;
                    margin-right: 0;

                    &:last-child {
                        margin-left: 0;
                    }
                }

                &.split {
                    .col {
                        @include flex;

                        .btn {
                            margin-left: 10px;

                            &:first-child {
                                margin-left: 0;
                            }
                        }
        
                        &:first-child {
                            text-align: right;
                        }
                    }
                }
            }
        }
    }

    // 투자, 대출 현황
    .invest-status,
    .loan-status {
        @include default-width(1200px);

        .h2-tit {
            height: 60px;
            border-bottom: 2px solid $basecolor;
            font-size: 32px;
            font-weight: 800;
        }

        .status-top {
            margin: 40px 0 120px;

            @include clear;

            [class^=status-] {
                float: left;
                width: calc((100% - 60px) / 2);
            }

            .status-cnt {
                float: left;
                padding-top: 15px;

                @include clear;

                li {
                    float: left;
                    width: calc(100% / 3);
                    font-size: 24px;
                    font-weight: 800;
                    text-align: center;
                }

                .cnt {
                    display: block;
                    margin-bottom: 10px;
                    font-size: 80px;
                }
            }

            .status-list {
                margin-left: 60px;

                .list {
                    li {
                        position: relative;
                        padding: 15px 0 15px 20px;
                        border-top: 1px solid #dfefee;
                        font-size: 16px;

                        @include flexbox;
                        @include justify-content(space-between);

                        .conts {
                            font-weight: 800;
                        }

                        .btn-lnk {
                            color: $plumcolor;
                            text-decoration: underline;
                        }

                        &:before {
                            content: "";
                            position: absolute;
                            left: 0;
                            top: 50%;
                            margin-top: -2px;
                            background: $basecolor;

                            @include size(4px);
                            @include border-radius(100px);
                        }

                        &:first-child {
                            border-top: 0;
                        }
                    }

                    &.total {
                        .tit {
                            color: $basecolor1;

                            strong {
                                color: $basecolor;
                            }
                        }

                        .conts {
                            font-weight: 800;

                            &.up {
                                color: $pred;
                            }

                            &.down {
                                color: $pblu;
                            }
                        }
                    }

                    &.noti {
                        font-weight: 700;

                        .cnt {
                            color: $plumcolor;
                            font-weight: 700;
                        }
                    }

                    & + .list {
                        li {
                            &:first-child {
                                border-top: 1px solid #dfefee;
                            }
                        }
                    }
                }
            }
        }

        .status-box {
            position: relative;
            // margin-top: 90px;
            padding-bottom: 25px;

            // 범례
            .status-legend {
                margin-bottom: 30px;

                @include clear;

                li {
                    position: relative;
                    float: left;
                    margin-right: 15px;
                    padding-right: 16px;
                    font-size: 24px;
                    font-weight: 700;

                    &:after {
                        content: "";
                        position: absolute;
                        right: 0;
                        top: 50%;
                        width: 1px;
                        height: 18px;
                        margin-top: -9px;
                        background: #ccc;
                    }

                    .cnt {
                        color: $plumcolor;
                    }

                    &:last-child {
                        margin-right: 0;
                        padding-right: 0;

                        &:after {
                            display: none;
                        }
                    }
                }
            }

            .board-wrap {
                position: relative;
            }

            .board {
                .slt {
                    padding: 3px;
                    font-size: 14px;
                }
            }

            .btn-del {
                position: absolute;
                right: 0;
                top: -66px;
                width: 160px;
                height: 48px;
                font-size: 16px;
                font-weight: 700;
            }

            .paging {
                margin-top: 25px;
            }

            & + .status-box {
                padding-top: 90px;
                border-top: 2px solid #ededed;
            }
        }

        .status-top + .status-box {
            border-top: 0;
        }
    }
}