@charset "utf-8";

.plum-about {
    padding-bottom: 200px;

    .about-head {
        background: $plumcolor;
    }

    // 공지사항
    .section-notice {
        // 리스트
        .notice-list {
            @include default-width(1200px);
    
            .board {
                .txt-lft {
                    padding-left: 20px;
                    padding-right: 20px;
                    line-height: 1.4;
                }
            }
    
            .paging {
                margin-top: 25px;
            }
        }

        // 내용
        .notice-view {
            position: relative;

            @include default-width(1200px);

            .h2-tit {
                margin-right: 200px;
                font-size: 32px;
                font-weight: 800;
                line-height: 1.3;
            }

            .date {
                position: absolute;
                right: 24px;
                top: 0;
                font-size: 24px;
                font-weight: 700;
                line-height: 41px;
            }

            .contents {
                margin-top: 30px;
                padding: 50px 0;
                border-top: 2px solid $basecolor1;
                border-bottom: 2px solid $basecolor1;
                line-height: 1.5;
            }
        }

        .notice-bottom {
            margin-top: 40px;
            text-align: center;

            .btn-xlarge {
                width: 280px;
            }
        }
    }

    // 뉴스
    .section-news {
        .news-wrap {
            overflow: hidden;

            @include default-width(1200px);
        
            .news-items {
                margin: 0 -20px -60px;
        
                @include flexbox;
                @include flex-wrap(wrap);
        
                .item {
                    width: calc(100% / 3);
                    margin-bottom: 60px;
                    padding: 0 20px;
                    text-align: center;
        
                    @include flex(none);
                    @include box-sizing;
        
                    a {
                        display: block;
        
                        &:hover {
                            .subject {
                                text-decoration: underline;
                            }
                        }
                    }
        
                    &-picture-img {
                        object-fit: cover;
                        vertical-align: top;
                    }
        
                    &-content {
                        margin-top: 20px;
        
                        &:after {
                            content: "자세히 보기";
                            display: block;
                            margin-top: 20px;
                            font-size: 15px;
                            color: $plumcolor;
                            font-weight: 700;
                        }
                    }
        
                    .pressname {
                        display: block;
                        height: 32px;
                        font-size: 20px;
                        line-height: 32px;
        
                        &-img {
                            height: 32px;
                        }
                    }
        
                    .subject {
                        height: calc(20px * 2 * 1.4);
                        margin-top: 10px;
                        font-size: 20px;
                        line-height: 1.4;
        
                        @include truncate2;
                    }
        
                    .date {
                        display: block;
                        margin-top: 15px;
                        font-size: 16px;
                        color: $basecolor2;
                    }
        
                    .detail {
                        height: calc(16px * 1.4 * 3);
                        margin-top: 15px;
                        font-size: 16px;
                        color: $basecolor1;
                        line-height: 1.4;
        
                        @include truncate2(null, 3);
                    }
                }
            }
        
            .news-more {
                margin-top: 40px;
                text-align: center;
            }
        }
    }

    // 이용안내
    .section-guide {
        .guide-wrap {
            @include default-width(1200px);

            .guide-list {
                margin-top: 150px;
            }

            .list-items {
                display: none;

                .h2-tit {
                    margin-bottom: 200px;
                    font-size: 40px;
                    text-align: center;
                    line-height: 1.2;
                }

                &.active {
                    display: block;
                }
            }

            .guide-step {
                margin-top: 170px;
                padding-top: 120px;
                border-top: 1px solid #ccc;

                @include flexbox;

                li {
                    position: relative;
                    padding: 0 30px;
                    border-left: 1px solid #E6E7E8;
                    font-size: 14px;
                    line-height: 1.4;

                    @include flex;
                    @include box-sizing;

                    .t {
                        display: block;
                        margin-bottom: 40px;
                        font-size: 24px;
                        font-weight: 800;
                        color: $plumcolor;
                        line-height: 1;
                    }

                    &:first-child {
                        padding-left: 0;
                        border-left: 0;
                    }

                    &:last-child {
                        padding-right: 0;
                    }
                }
            }
        }
    }

    // 회사소개
    .section-company {
        .company-wrap {
            @include default-width(1200px);

            .company-list {
                margin-top: 150px;
            }

            .list-items {
                display: none;

                .h2-tit {
                    margin-bottom: 30px;
                    font-size: 40px;
                    text-align: center;
                    line-height: 1.2;
                }

                .dsc {
                    font-size: 16px;
                    line-height: 1.4;
                    text-align: center;
                }

                .company-intro {
                    margin-top: 80px;
                    padding: 70px 40px 0;
                    border-top: 1px solid #ccc;
                    font-size: 24px;
                    line-height: 1.5;
                    // text-align: center;

                    .paragraph {
                        margin-top: 50px;

                        &.close {
                            text-align: right;
                        }

                        &:first-child {
                            margin-top: 0;
                        }
                    }
                }

                .address-map {
                    @include flexbox;

                    .map-item {
                        margin-right: 60px;

                        @include flex;

                        .root_daum_roughmap {
                            width: 100% !important;

                            @include box-sizing;
                        }

                        .map-info {
                            margin-top: 40px;

                            li {
                                margin-top: 25px;
                                font-size: 16px;
                                line-height: 1.3;

                                &.name {
                                    font-size: 32px;
                                    font-weight: 700;
                                }

                                &.tel {
                                    color: $basecolor2;
                                }

                                &:first-child {
                                    margin-top: 0;
                                }
                            }
                        }

                        &:last-child {
                            margin-right: 0;
                        }
                    }
                }

                &.active {
                    display: block;
                }
            }
        }
    }
}