@charset "utf-8";

#header {
    .header-wrap {
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 81px;
        border-bottom: 1px solid #f4f4f4;
        background: #fff;
        z-index: 9;

        @include flexbox;
        @include align-items(center);
        @include box-sizing;
        @include transition(height .5s);
    }

    .header {
        @include default-width(1600px);
        @include flex;
        @include flexbox;
        @include justify-content(space-between);
        @include align-items(center);
    }

    .bi {
        position: relative;
        height: 40px;
        z-index: 10;

        @include transition(height .5s);

        .img-bi {
            max-height: 100%;
        }
    }

    // aside menu
    .header-menu {
        position: relative;
        z-index: 10;

        .menu-item {
            @include flexbox;
            @include align-items(center);

            > li {
                margin-left: 30px;

                @include flex(none);

                &:first-child {
                    margin-left: 0;
                }
            }

            .t {
                position: relative;
                display: block;
                font-size: 16px;
                font-weight: 700;
                z-index: 12;
                cursor: pointer;

                &:hover {
                    color: $hover;
                }
            }

            .alarm {
                position: relative;
                background: url("https://webpi.github.io/doplum/assets/img/comm/ico_alram.png") no-repeat 50% 50%;
                background-size: 20px auto;

                @include size(30px);

                .badge {
                    position: absolute;
                    right: -6px;
                    top: -6px;
                    min-width: 18px;
                    height: 18px;
                    padding: 3px 4px 2px;
                    font-size: 12px;
                    color: #fff;
                    background: $plumcolor;
                    line-height: 13px;
                    text-align: center;

                    @include box-sizing;
                    @include border-radius(100px);
                }
            }

            .mypage {
                background: url("https://webpi.github.io/doplum/assets/img/comm/ico_men.png") no-repeat 50% 50%;
                background-size: 20px auto;

                @include size(30px);
            }

            // 고객지원
            .support {
                position: relative;

                .item-sub {
                    left: 50%;

                    @include translate(-50%, 0);

                    .box:before {
                        left: 50%;
                        margin-left: -10px;
                    }

                    li {
                        margin-top: 12px;
                        font-size: 15px;
                        white-space: nowrap;

                        a:hover {
                            color: $hover;
                        }

                        &:first-child {
                            margin-top: 0;
                        }
                    }
                }
            }

            // 알림
            .notifications {
                .item-sub {
                    right: 0;

                    .box:before {
                        right: 64px;
                    }
                }

                .item-noti {
                    width: 480px;

                    li {
                        border-top: 1px solid #dedede;

                        &:first-child {
                            border-top: 0;
                        }
                    }

                    a {
                        display: block;
                        padding: 14px 0;
                        font-size: 14px;
                        line-height: 1.2;

                        @include flexbox;
                        @include align-items(center);

                        .noti-txt {
                            margin-right: 20px;
                            @include flex;
                        }

                        .view {
                            white-space: nowrap;
                        }

                        &:hover {
                            .view {
                                color: $hover;
                            }
                        }
                    }
                }
            }
        }
    }

    // global navigation
    .gnb {
        position: absolute;
        left: 0;
        top: 50%;
        width: 100%;
        
        @include flexbox;
        @include justify-content(center);
        @include translate(0, -50%);

        .gnb-item {
            @include clear;

            > li {
                position: relative;
                float: left;
                margin-left: 40px;
                
                &:first-child {
                    margin-left: 0;
                }
            }

            .t {
                position: relative;
                font-size: 20px;
                font-weight: 700;
                z-index: 12;
                cursor: pointer;

                &:hover {
                    color: $hover;
                }
            }

            .item-sub {
                width: 480px;

                .box {
                    &:before {
                        left: 58px;
                    }
                }
            }

            .sub-dsc {
                font-size: 14px;
                line-height: 1.3;
            }

            .sub-list {
                margin-top: 30px;

                .list-box {
                    @include clear;
                    
                    li {
                        position: relative;
                        float: left;
                        width: 50%;
                        padding-left: 12px;
                        font-size: 16px;
                        font-weight: 700;

                        @include box-sizing;

                        a:hover {
                            color: $hover;
                        }

                        &:before {
                            content: "";
                            position: absolute;
                            left: 0;
                            top: 5px;
                            background: #222;

                            @include size(6px);
                            @include border-radius(100px);
                        }
                    }
                }

                &.col2 {
                    @include clear;

                    .list-box {
                        width: 50%;
                        float: left;

                        li {
                            float: none;
                            width: 100%;
                            margin-top: 24px;

                            &:first-child {
                                margin-top: 0;
                            }
                        }
                    }
                }
            }
        }
    }

    // 서브 메뉴
    .item-sub {
        visibility: hidden;
        opacity: 0;
        position: absolute;
        top: 50%;
        padding-top: 40px;
        z-index: -9999;

        .box {
            position: relative;
            padding: 24px;
            border: 1px solid #999;
            background: #fff;
         
            @include box-shadow(0 2px 3px 0 rgba(38, 59, 94, 0.1));

            &:before {
                content: '';
                position: absolute;
                top: -11px;
                display: block;
                width: 20px;
                height: 22px;
                background: url("https://webpi.github.io/doplum/assets/img/comm/ico_tiptop.png") no-repeat 0 0;
                background-size: cover;
            }
        }
    }

    .is-sub {
        &:hover {
            .item-sub {
                visibility: visible;
                opacity: 1;
                z-index: 11;
            }
        }
    }

    // 모바일
    .btn-mobile {
        display: none;
        position: absolute;
        right: 24px;
        top: 50%;
        border: 0;
        font-size: 24px;
        background: #fff;
        z-index: 10;

        @include size(24px);
        @include translate(0, -50%);
    }

    .is-mobile {
        .mobile-wrap {
            overflow-y: auto;
            overflow-x: hidden;
            position: fixed;
            right: 0;
            top: 0;
            width: 80vw;
            height: 100vh;
            background: #fff;
            z-index: 101;
            
            @include transform(translate3d(100%, 0, 0));
            @include transition(transform .4s);
        }

        .mobile-menu {
            padding: 40px 0 100px;
        }

        .menu-login {
            padding: 80px 20px 0;
            
            .button-group {
                font-size: 0;

                @include flexbox;
            }
        }

        .mobile-gnb {
            .gnb-top {
                margin-bottom: 40px;
                padding: 0 20px;

                .greetings {
                    display: block;
                    min-height: 40px;
                    margin-bottom: 40px;
                    font-size: 20px;
                    line-height: 40px;
                }
            }

            .gnb-item {
                .t {
                    display: block;
                    height: 32px;
                    padding: 0 20px;
                    font-size: 14px;
                    color: #666;
                    background: #f9f9f9;
                    line-height: 32px;         
                }

                .list-box {
                    padding: 20px 0 40px;
                    font-size: 18px;

                    li {
                        margin-top: 30px;
                        padding: 0 32px;

                        &:first-child {
                            margin-top: 0;
                        }
                    }
                }
            }

            .gnb-bottom {
                padding: 25px 20px 0;
                border-top: 1px solid #ededed;
            }

            .logout {
                font-size: 18px;
            }
        }

        .btn-mobile-clse {
            position: absolute;
            right: 20px;
            top: 40px;
            border: 0;
            background: transparent;
            font-size: 30px;

            @include size(40px);
        }

        &.on {
            .mobile-wrap {
                @include transform(translate3d(0, 0, 0));
            }
        }
    }
}

// tablet 768 ~ 1200
@include respond-to(tablet) {
    #header {
        .header-wrap {
            height: 49px;
        }

        .bi {
            height: 26px;
        }

        .header-menu {
            display: none;
        }

        .gnb {
            display: none;
        }

        .btn-mobile {
            display: block;
        }
    }
}

// tablet 767
@include respond-to(mobiles) {
    #header {
        .btn-mobile {
            right: 20px;
        }
    }
}