@charset "utf-8";

.plum-index {
    .section-invest {
        .invest-head {
            text-align: center;

            @include default-width(1600px);

            .h1-tit {
                padding-top: 136px;
                font-size: 100px;
                font-weight: 800;
            }

            .dsc {
                margin: 25px 0 30px;
                font-size: 24px;
                line-height: 1.4;

                strong {
                    display: block;
                    margin-bottom: 15px;
                    font-size: 40px;
                }
            }
        }

        .invest-item {
            .h2-tit {
                font-size: 64px;
                font-weight: 800;
            }

            .h3-tit {
                margin-top: 80px;
                font-size: 40px;
            }

            .dsc {
                margin-top: 30px;
                font-size: 20px;
                color: $basecolor1;
                line-height: 1.3;
            }

            .button-group {
                margin-top: 40px;
            }

            &.loan-service {
                margin-top: 160px;
                padding-top: 80px;
                padding-bottom: 90px;
                background: url($url-images + "/main/main_fig01.jpg") no-repeat 100% 50%;
                background-size: contain;

                @include default-width(1600px);
            }

            &.rent {
                margin-top: 150px;
                padding-bottom: 600px;
                background: url($url-images + "/main/main_fig02.png") no-repeat 50% 100%;
                // background-size: contain;

                .box {
                    @include default-width(1600px);
                }
            }
        }
    }

    .section-ai {
        margin-top: 170px;

        @include default-width(1200px);

        .ai-head {
            text-align: center;

            .h1-tit {
                font-size: 80px;
                font-weight: 800;
            }
    
            .h2-tit {
                margin-top: 30px;
                font-size: 40px;
            }
    
            .dsc {
                margin-top: 30px;
                font-size: 24px;
            }
        }

        .ai-intro {
            margin-top: 280px;

            li {
                position: relative;
                margin-top: 270px;
                padding-left: 200px;
                font-size: 20px;

                &:before {
                    content: "";
                    position: absolute;
                    left: 0;
                    top: 0;
                    width: 120px;
                    height: 4px;
                    font-size: 0;
                    line-height: 0;
                    background: #222;

                    @include border-radius(10px);
                }

                &:after {
                    content: "";
                    position: absolute;
                    right: 0;
                    top: 50%;
                    z-index: -1;

                    @include translate(0, -50%);
                }

                &.diverse {
                    &:after {
                        width: 400px;
                        height: 322px;
                        background: url($url-images + "/main/tech_fig01.png") no-repeat 0 0;
                    }
                }

                &.smart {
                    &:after {
                        width: 401px;
                        height: 262px;
                        background: url($url-images + "/main/tech_fig02.png") no-repeat 0 0;
                    }
                }

                &.dynamic {
                    &:after {
                        width: 400px;
                        height: 323px;
                        background: url($url-images + "/main/tech_fig03.png") no-repeat 0 0;
                    }
                }

                &:first-child {
                    margin-top: 0;
                }
            }

            .h3-tit {
                margin-bottom: 60px;
                font-size: 52px;
                font-weight: 800;
            }

            .tit {
                display: block;
                margin-bottom: 30px;
                font-size: 32px;
            }

            .txt {
                line-height: 1.3;
            }
        }
    }

    // 문의하기
    .question {
        margin-top: 230px;

        .cpn-question {
            background: #f7f7f7;
        }
    }

    // news & press
    .section-news {
        margin-top: 265px;

        @include default-width(1200px);
        @include clear;

        .h1-tit {
            position: relative;
            float: left;
            margin-right: 200px;
            padding-left: 200px;
            font-size: 52px;
            font-weight: 800;

            &:before {
                content: "";
                position: absolute;
                left: 0;
                top: 0;
                width: 120px;
                height: 4px;
                font-size: 0;
                line-height: 0;
                background: #222;

                @include border-radius(10px);
            }
        }

        .news-items {
            overflow: hidden;

            .item {
                margin-top: 35px;

                a {
                    display: block;
            
                    @include flexbox;
                    @include align-items(center);

                    &:hover {
                        .h2-tit {
                            color: $hover;
                        }
                    }
                }

                .img {
                    margin-right: 35px;
                    border: 1px solid #efefef;

                    @include flex(none);
                    @include size(200px);
                    @include border-radius(10px);
                    @include box-sizing;
                }
            
                .medialist-info {
                    @include flex();

                    .h2-tit {
                        font-size: 24px;
                        line-height: 1.3;

                        @include truncate2;
                    }
                }
            
                .date {
                    display: block;
                    margin-top: 12px;
                    font-size: 16px;
                    color: $basecolor2;
                }

                &:first-child {
                    margin-top: 0;
                }
            }
        }
    }

    // 누적 대출액
    .cumulative-loan {
        margin-top: 200px;
        padding: 114px 0 120px;
        color: #fff;
        background:#7872ff;
        text-align: center;

        .box {
            @include default-width(1200px);
        }

        .total {
            display: block;
            font-size: 80px;

            .txt {
                display: block;
                margin-top: 28px;
                font-size: 24px;
            }
        }

        .loan-detail {
            margin-top: 70px;
            padding: 42px 0;
            border: 1px solid rgba(255,255,255,0.16);
            
            @include flexbox;

            li {
                min-height: 80px;
                border-left: 1px solid rgba(255,255,255,0.2);
                font-size: 16px;

                @include flex;
                @include box-sizing;
                @include flexbox;
                @include justify-content(center);
                @include flex-direction(column);

                &:first-child {
                    border-left: 0;
                }
            }

            .num {
                display: block;
                margin-top: 18px;
                font-size: 32px;
            }
        }
    }
}