@charset "utf-8";

// tablet 768 ~ 1200
@include respond-to(tablet) {
    // main
    .plum-index {        
        // news & press
        .section-news {
            .h1-tit {
                float: none;
                margin-right: 0;

                br {
                    display: none;
                }
            }

            .news-items {
                margin-top: 60px;
            }
        }
    }

    // 투자
    .plum-invest {
        // 투자 이용 안내
        .section-guide {
            .invest-direct {
                .direct-product {
                    .product-items {
                        li {
                            margin-right: 20px;
                            padding: 55px 20px;
                            text-align: center;
                        }
                    }
                }
            }
        }

        // 투자하기
        .section-invest {
            // 투자 상품
            .invest-product {
                .product-info {
                    width: 50%;

                    @include flex-direction(column);

                    .info-items {
                        margin-top: 40px;

                        &:first-child {
                            margin-top: 0;
                        }
                    }
                }

                .product-revenue {
                    width: calc(50% - 60px);
                }
            }
        }

        // 자동 투자
        .section-auto-invest {
            .auto-invest-info {
                > li {
                    float: none;

                    &.col {
                        width: auto;

                        & + .col {
                            margin-left: 0;
                        }
                    }
                }
            }

            .auto-invest-set {
                .set-condition {
                    @include flex-direction(column);
                    
                    .condition-chart {
                        margin: 0 auto 80px;
                    }
                }
            }
        }
    }

    // 대출
    .plum-loan {
        // 대출 이용 안내
        .section-guide {
            .loan-direct {
                .direct-insert {
                    &:before,
                    &:after {
                        bottom: 23.5px;
                    }
                }
            }
        }

        // 대출 신청
        .section-loan {
            .loan-step {
                .step-info {
                    .info-list {
                        .item {
                            .box {
                                @include flex-direction(column);
                            }

                            .col {
                                max-width: none;
                                margin-left: 0;
                                margin-top: 10px;

                                &:first-child {
                                    margin-top: 0;
                                }
                            }

                            &:last-child {
                                margin-right: 0;
                            }
                        }

                        .btn-adrs {
                            width: 100%;
                        }
                    }
                }
            }
        }
    }

    // 고객지원
    .plum-cscenter {
        // 문의하기
        .section-qna {
            // 쓰기
            .qna-write {
                .write-contents {
                    .contents {
                        .box {
                            @include flex-direction(column);

                            .col {
                                width: auto;
                                max-width: none;
                                margin-left: 0;
                            }
                        }
                    }
                }
            }
        }
    }

    // 마이페이지
    .plum-mypage {
        // 요약
        .section-dashboard {
            .status-items {
                margin-top: 60px;
    
                .item {
                    position: relative;
                    float: none;
                    width: auto;
                    margin-top: 60px;
                    margin-left: 0;

                    &:first-child {
                        margin-top: 0;
                    }
                }
    
                &:first-child {
                    margin-top: 0;
                }
            }
        }

        // 투자
        .section-invest {
            // 진행중
            .invest-ready {
                .invest-bottom {
                    .btn-xlarge {
                        width: 160px;
                    }
                }
            }
        }

        // 대출
        .section-loan {
            // 진행중
            .loan-state {
                .loan-bottom {
                    .btn-xlarge {
                        width: 160px;
                    }
                }
            }
        }
    }
}

// mobile 767
