                        .tit {
                            display: block;
                            width: auto;
                            margin-bottom: 20px;
                            padding-left: 0;
                            font-size: 16px;
    
                            &:before {
                                display: none;
                            }
                        }
                    }
                }

                .application-set {
                    margin-top: 0;
                    padding-top: 40px;
                    border-top: 1px solid #f4f4f4;
    
                    .set-condition {
                        // @include clear;

                        @include flex-direction(column);
    
                        .condition-chart {
                            margin: 0 auto 40px;
                        }

                        .cpn-sliders {
                            .item {
                                .tit {
                                    padding-left: 0;
        
                                    &:before {
                                        display: none;
                                    }
                                }
                            }
                         }
                    }
    
                    .set-date {
                        margin-top: 60px;

                        @include flex-direction(column);
                        @include align-items();

                        .tit {
                            display: block;
                            width: auto;
                            margin-bottom: 20px;
                            padding: 0;
                            font-size: 16px;
                            
                            &:before {
                                display: none;
                            }
                        }

                        .idate {
                            @include flexbox;
                            @include align-items(center);

                            .istyle {
                                max-width: none;
                            }
                        }
                    }
                }

                .application-check {
                    margin-top: 40px;
                }

                .application-bottom {
                    margin-top: 40px;
    
                    .button-group {
                        @include flex-direction(column);
                        @include justify-content(center);
                    }

                    .group {
                        @include justify-content(center);
                    }

                    .btn-product-app {
                        margin-top: 20px;
                        margin-left: 0;
                    }
                }
            }

            // 대여 상품 등록 신청 완료
            .lease-result {
                .result-top {
                    height: 30px;
                    margin-bottom: 10px;
    
                    .h2-tit {
                        font-size: 20px;
                    }
                }
            }

            .required-notice {
                margin-top: 40px;
            }

            .lease-footer {
                margin-top: 40px;

                .button-group {
                    @include flexbox;
                    @include justify-content(center);
                }
            }

            // 상품 검색
            .lease-search {
                .search-top {
                    margin-bottom: 80px;

                    .btn-lease-search {
                        top: -50px;
                    }

                    .top-button {
                        margin-top: 40px;
                    }
                }

                .search-result {
                    .h3-tit {
                        margin-bottom: 20px;
                        font-size: 16px;
                    }
                }
            }

            // 약관 체크
            .lease-check {
                margin-top: 40px;
            }

            .lease-bottom {
                margin-top: 30px;
                padding: 30px 0;

                &.fixed {
                    position: fixed;
                    left: 0;
                    bottom: 0;
                    width: 100%;
                    margin: 0;
                    padding: 0;
                    background: none;
                    z-index: 2;

                    .button-group {
                        padding: 0;
                    }
                    
                    .btn {
                        width: 50%;
                        max-width: none;
                        margin: 0;

                        @include border-radius(0);
                        @include transition(.0s);

                        &.btn-white {
                            background: #f9f9f9;
                        }
                    }
                }
            }
        }
    }

    // 회원 가입
    .plum-user {
        padding-bottom: 100px;
    
        .section-join {
            // 가입
            .user-join {
                .join-top {
                    .h2-tit {
                        font-size: 24px;
                    }
    
                    .dsc {
                        margin-top: 20px;
                        font-size: 14px;
                    }
                }
    
                .join-info {
                    margin-top: 60px;
                }
    
                .info-contents {
                    li {
                        padding: 24px 0;

                        @include flex-direction(column);
    
                        &:first-child {
                            border-top: 1px solid #ededed;
                        }
                    }
    
                    .tit {
                       margin-right: 0;
                       margin-bottom: 20px;
                       font-size: 16px;

                       strong {
                            line-height: inherit;
                       }
                    }
                }
       
                .info-button {
                    margin-top: 30px;
                }
            }

            // 가입 인증
            .user-auth {
                .h2-tit {
                    font-size: 24px;
                    line-height: 1.3;
                }

                .dsc {
                    margin: 20px 0 35px;
                    font-size: 14px;
                }
            }
        }
    }

    // 고객지원
    .plum-cscenter {
        padding-bottom: 100px;

        // 자주하는 질문
        .section-faq {
            .faq-wrap {
                .faq-list {
                    margin-top: 20px;
                }

                .item {
                    .h3-tit {
                        height: 60px;
                        padding: 0 56px 0 20px;
                        font-size: 16px;

                        .letter-first {
                            font-size: 20px;
                        }

                        i {
                            right: 20px;
                        }
                    }

                    .content {
                        padding: 20px;
                    }
                }
            }
        }

        // 문의하기
        .section-qna {
            // 쓰기
            .qna-write {
                .write-contents {
                    li {
                        padding: 24px 0;
    
                        @include flex-direction(column);
                    }

                    .col {
                        max-width: none;
                    }
    
                    .tit {
                        width: auto;
                        margin-right: 0;
                        margin-bottom: 20px;
                        font-size: 16px;
    
                        strong {
                                line-height: inherit;
                        }
                    }
                }

                .write-terms-check {
                    margin-top: 30px;
                }

                .qna-button {
                    padding-top: 0;
                }
            }

            // 보기
            .qna-view {
                .h2-tit {
                    margin-bottom: 14px;
                    font-size: 20px;
                }

                .contents-info {
                    display: block;
                    
                    th,
                    td {
                        display: block;
                        font-size: 14px;
                    }

                    td {
                        &.contents {
                            padding: 25px 0;
                        }
                    }
                }

                // 댓글
                .view-comment {
                    margin-top: 30px;
                }
            }

            .qna-button {
                margin-top: 40px;
            }
        }
    }

    // 플럼
    .plum-about {
        padding-bottom: 100px;

        // 공지사항
        .section-notice {
            // 내용
            .notice-view {
                .h2-tit {
                    margin-right: 0;
                    font-size: 20px;
                }

                .date {
                    display: block;
                    position: static;
                    margin-top: 15px;
                    font-size: 14px;
                    line-height: 1;
                }

                .contents {
                    margin-top: 30px;
                    padding: 50px 0;
                    border-top: 2px solid $basecolor1;
                    border-bottom: 2px solid $basecolor1;
                    line-height: 1.5;
                }
            }

            .notice-bottom {
                .btn-xlarge {
                    width: 160px;
                }
            }
        }

        // 뉴스 
        .section-news {
            .news-wrap {
                .news-items {
                    .item {
                        width: calc(100% / 2);
                    }
                }
            }
        }

        // 이용안내
        .section-guide {
            .guide-wrap {
                .guide-list {
                    margin-top: 60px;
                }

                .list-items {
                    .h2-tit {
                        margin-bottom: 60px;
                        font-size: 20px;
                    }
                }

                .guide-step {
                    margin-top: 100px;
                    padding-top: 80px;

                    @include flex-direction(column);

                    li {
                        margin-top: 100px;
                        padding: 0;
                        border: 0;
                        text-align: center;

                        .t {
                            margin-bottom: 20px;
                        }

                        &:first-child {
                            margin-top: 0;
                        }
                    }
                }
            }
        }

        // 회사소개
        .section-company {
            .company-wrap {
                .company-list {
                    margin-top: 40px;
                }

                .list-items {
                    .h2-tit {
                        margin-bottom: 15px;
                        font-size: 24px;
                    }

                    .dsc {
                        font-size: 14px;
                    }

                    .company-intro {
                        margin-top: 40px;
                        padding: 40px 0 0;
                        font-size: 14px;
                        line-height: 1.5;
                        // text-align: center;

                        .paragraph {
                            margin-top: 30px;
                        }
                    }

                    .address-map {
                        @include flex-direction(column);

                        .map-item {
                            margin-top: 50px;
                            margin-right: 0;

                            .map-info {
                                margin-top: 20px;

                                li {
                                    margin-top: 20px;
                                    font-size: 14px;

                                    &.name {
                                        font-size: 24px;
                                    }
                                }
                            }

                            &:first-child {
                                margin-top: 0;
                            }
                        }
                    }
                }
            }
        }
    }

    // 마이페이지
    .plum-mypage {
        padding-bottom: 100px;
        
        .mypage-head {
            position: relative;
            padding-bottom: 0;

            .box {
                height: 70px;
            }
    
            .lnb-wrap {
                padding-right: 40px;

                .lnb {
                    overflow-x: auto;
                    overflow-y: hidden;

                    @include smoothScroll;

                    .btn-lnb {
                        height: 40px;
                        padding: 0 10px;
                        font-size: 14px;
    
                        &.active {
                            border-bottom: 4px solid #ffc033;
                        }
                    }
                }
    
                // 모바일 전체보기 버튼
                .btn-lnb-mobile {
                    display: block;
                }
            }
        }

        // 요약
        .section-dashboard {
            .status-items {
                .item {
                    .h2-tit {
                        height: 34px;
                        font-size: 20px;
                    }

                    .box {
                        margin-top: 30px;
                    }

                    .summary {
                        li {
                            font-size: 16px;
                        }

                        .cnt {
                            margin-bottom: 5px;
                            font-size: 40px;
                        }
                    }
                    
                    .list {
                        li {
                            padding: 10px 0 10px 15px;
                            font-size: 13px;
                        }

                        &.total {
                            margin-top: 30px;
                        }
                    }

                    .btn-more {
                        font-size: 13px;
                        line-height: 20px;
                    }

                    .nodata {
                        strong {
                            font-size: 20px;
                        }

                        .lnk {
                            margin-top: 10px;
                            font-size: 13px;
                        }
                    }
                }
            }
        }

        // 투자
        .section-invest {
            // 현황
            .invest-status {
                .h2-tit {
                    height: 34px;
                    font-size: 20px;
                }

                .status-top {
                    margin-top: 30px;

                    [class^=status-] {
                        float: none;
                        width: auto;
                    }

                    .status-cnt {
                        padding-top: 0;

                        li {
                            font-size: 16px;
                        }

                        .cnt {
                            margin-bottom: 5px;
                            font-size: 40px;
                        }
                    }

                    .status-list {
                        margin-left: 0;

                        .list {
                            li {
                                padding: 10px 0 10px 15px;
                                font-size: 13px;
                            }
    
                            &.total {
                                margin-top: 30px;
                            }
                        }
                    }
                }

                .invest-row {
                    margin-top: 40px;

                    // 범례
                    .invest-legend {
                        margin-bottom: 15px;

                        li {
                            margin-right: 9px;
                            padding: 6px 10px 6px 0;
                            font-size: 13px;

                            &:after {
                                height: 12px;
                                margin-top: -7px;
                            }
                        }
                    }

                    .row-button {
                        margin-top: 60px;
                        text-align: center;
                    }

                    .btn-del {
                        position: static;
                        font-size: 14px;
                    }

                    & + .invest-row {
                        margin-top: 40px;
                    }
                }
            }

            // 진행중
            .invest-ready {
                .invest-product {
                    .box {
                        float: none;
                        width: auto;
                        margin-left: 0;
                        margin-top: 25px;
        
                        .h2-tit {
                            height: 34px;
                            font-size: 20px;
        
                            .addinfo {
                                display: none;
                            }
                        }

                        &:first-child {
                            margin-top: 0;
                        }
                    }
                }
            }

            // 완료
            .invest-detail {
                .detail-top {
                    position: relative;
                    height: 30px;
                    margin-bottom: 30px;
                    border-bottom: 2px solid $basecolor;

                    .h2-tit {
                        font-size: 20px;
                    }

                    .purpose {
                        font-size: 16px;
                        line-height: 20px;
                    }
                }

                .detail-info {
                    margin-top: 40px;

                    .current-revenue {
                        float: none;
                        width: auto;
                        font-size: 28px;
                    }

                    .product-info {
                        float: none;
                        width: auto;
                        margin-right: 0;
                        margin-top: 30px;

                        .total {
                            order: -1;
                            margin: 0 0 40px;
                            font-size: 28px;
                            line-height: 1.4;
                        }
                    }

                    .invest-info {
                        & + .invest-info {
                            margin-top: 40px;
                            padding-top: 0;
                            border-top: 0;
                        }
                    }
                }

                .invest-bottom {
                    margin-top: 60px;

                    .btn-xlarge {
                        width: 160px;
                    }
                }
            }

            .invest-bottom {
                margin-top: 0;

                .button-group {
                    &.split {
                        @include flex-direction(column);

                        .col {
                            margin-top: 40px;
    
                            @include flexbox;
                            @include justify-content(center);
    
                            &:first-child {
                                margin-top: 0;
                            }
                        }
                    }
                }
            }

            // 자동 투자 리스트
            .invest-list {
                .list-top {
                    height: auto;
                    margin-bottom: 30px;
                    border-bottom: 0;

                    .h2-tit {
                        font-size: 20px;
                    }

                    .top-button {
                        position: static;
                        margin-top: 20px;
                        text-align: right;
                    }
                }

                .paging {
                    margin-top: 25px;
                }
            }
        }

        // 대출
        .section-loan {
            .loan-state {
                .state-top {
                    height: 30px;
                    margin-bottom: 10px;

                    .h2-tit {
                        font-size: 20px;
                    }

                    .purpose {
                        font-size: 16px;
                        line-height: 20px;
                    }
                }

                .state-noti {
                    // overflow: hidden;
                    float: none;
                    width: auto;

                    .state-tit {
                        font-size: 28px;
                    }

                    .state-reason {
                        margin-top: 10px;
                        font-size: 14px;
                    }

                    .state-dsc {
                        font-size: 14px;
                    }
                }

                .product-info {
                    float: none;
                    width: auto;
                    margin-right: 0;
                    margin-top: 30px;
                }

                .required-notice {
                    margin-top: 40px;
                }
            }

            .loan-bottom {
                margin-top: 0;

                .button-group {
                    &.split {
                        @include flex-direction(column);

                        .col {
                            margin-top: 40px;

                            @include flexbox;
                            @include justify-content(center);

                            &:first-child {
                                margin-top: 0;
                            }
                        }
                    }
                }
            }
        }
    }
}

// mobiles small 480
@include respond-to(mobiles-small) {
    // 플럼
    .plum-about {
        // 뉴스 
        .section-news {
            .news-wrap {
                .news-items {
                    .item {
                        width: 100%;
                    }
                }
            }
        }
    }

    // 마이페이지
    .plum-mypage {
        // 투자
        .section-invest {
            // 자동 투자 리스트
            .invest-list {
                .list-top {
                    .top-button {
                        text-align: unset;

                        .btn {
                            width: 100%;
                            max-width: 100%;
                        }
                    }
                }
            }
        }
    }
}