@charset "utf-8";

// 타이틀
.layer-pop {
    display: none;
    overflow-y: auto;
    overflow-x: hidden;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    padding: 0 24px;
    z-index: 101;

    @include box-sizing;
    
    .pop-wrap {
        overflow: hidden;
        position: relative;
        width: 100%;
        max-width: 1200px;
        max-height: calc(100vh - 140px);
        min-height: 320px;
        margin: 70px auto;
        background: #fff;
        z-index: 101;

        @include flexbox;
        @include flex-direction(column);
    }

    .pop-head {
        position: relative;
        min-height: 115px;
        border-bottom: 2px solid #ededed;

        @include flex(none);
        @include flexbox;
        @include align-items(center);
        
        .tit {
            padding: 0 40px;
            font-size: 32px;
            font-weight: 800;

            @include flex;
        }
    }

    .pop-body {
        position: relative;
        overflow-y: auto;
        overflow-x: hidden;
    }

    .pop-footer {
        padding: 40px;
        border-top: 1px solid #ededed;
        text-align: center;

        @include flex(none);

        .button-group {
            @include flexbox;
            @include justify-content(center);
        }
    }

    .btn-layer-pop-clse {
        position: absolute;
        right: 44px;
        top: 42px;
        border: 0;
        font-size: 32px;
        background: #fff;

        @include size(32px);
    }

    // 공통
    .pop-comm {
        overflow: hidden;
        position: relative;
        width: 100%;
        max-width: 640px;
        max-height: calc(100vh - 140px);
        margin: 70px auto;
        background: #fff;
        z-index: 101;

        @include flexbox;
        @include flex-direction(column);

        .comm-msg {
            padding: 50px 40px 100px;
            font-size: 20px;
            text-align: center;
        }

        .pop-footer {
            padding-top: 0;
            border-top: 0;
        }
    }

    // 약관
    .terms-wrap {
        padding: 50px 40px 60px;
    }

    // 선택 상품 레이어
    .product-compare {
        padding: 0px 40px 60px;

        .board-compare{
            th,
            td {
                border: 1px solid #ededed;
            }

            th {
                padding: 0 20px;
                font-size: 16px;
                text-align: left;
            }

            td {
                height: 50px;
                padding: 0 10px;
                font-size: 16px;
                color: $basecolor1;
                text-align: right;
            }

            .compare-top {
                th,
                td {
                    border: 0;
                }

                td {
                    padding: 20px 10px;
                    text-align: center;
                }
            }

            .tit {
                padding: 0 10px;
                text-align: center;
            }

            .number {
                font-size: 20px;
                color: $plumcolor;
                text-align: center;
                text-decoration: underline;
            }

            .special {
                color: $plumcolor;
            }
        }
    }

    // 투자신청
    .application-invest {
        &-info {
            padding: 30px 40px;
            border-bottom: 1px solid #ededed;
            font-size: 16px;

            .info-items {
                @include clear;
            }

            li {
                float: left;
                width: calc((100% - 90px) / 2);
                height: 48px;
                margin-right: 90px;
                color: $basecolor1;

                &:nth-child(2n) {
                    margin-right: 0;
                }

                @include flexbox;
                @include align-items(center);
                @include justify-content(space-between);
            }

            .tit {
                position: relative;
                padding-left: 20px;
                color: $basecolor;
                font-weight: 700;

                &:before {
                    content: '';
                    position: absolute;
                    left: 0;
                    top: 50%;
                    display: block;
                    background: #222;

                    @include size(4px);
                    @include border-radius(100px);
                    @include translate(0, -50%);
                }
            }

            .istyle {
                max-width: 330px;
                background: #f9f9f9;
            }

            .dsc {
                margin-top: 20px;
                font-size: 14px;
                color: #4dabf7;

                @include flex(none);
            }
        }

        &-noti {
            padding: 50px 40px;
            border-bottom: 1px solid #ededed;

            .paragraph {
                margin-top: 30px;
                font-size: 16px;
                line-height: 1.5;

                &:first-child {
                    margin-top: 0;
                }
            }
        }

        &-submit {
            padding: 25px 0;
            font-size: 16px;
            font-weight: 700;
            text-align: center;

            .istyle {
                width: 100px;
                height: 26px;
                padding-top: 0;
                padding-bottom: 0;
                border-bottom: 2px solid $basecolor;
                font-size: 16px;
                text-align: center;
                vertical-align: middle;
                line-height: 26px;
            }

            .txt-confirm {
                display: block;
                margin-top: 15px;
            }
        }
    }

    // 설정 저장 목록
    .set-list {
        padding: 40px;

        .total {
            display: block;
            margin-bottom: 25px;
            font-size: 24px;

            .cnt {
                color: $plumcolor;
            }
        }
        
        .board {
            th,
            td {
                height: 56px;
            }

            .btn-del {
                border: 0;
                font-size: 14px;
                color: #ff0000;
                background: transparent;
                text-decoration: underline;
            }
        }

        .paging {
            margin-top: 25px;
        }
    }

    // 유저 정보 찾기
    .find-user {
        padding: 50px 40px 60px;
        text-align: center;

        .t {
            display: block;
            margin-bottom: 40px;
            font-size: 20px;
            line-height: 1.3;
        }

        .box {
            max-width: 360px;
            margin: 0 auto;
            text-align: left;

            & + .box {
                margin-top: 20px;
            }
        }

        .istyle {
            border: 1px solid #ccc;
            font-size: 16px;
        }

        .btn {
            margin-top: 40px;
        }
    }

    // 로그인
    .pop-login {
        max-width: 724px;

        .login {
            padding: 50px 40px 60px;
        }
    }

    // 문의하기 비밀글 비밀번호
    .secret-password {
        padding: 50px 40px 60px;
        text-align: center;

        .t {
            display: block;
            margin-bottom: 30px;
            font-size: 18px;
            line-height: 1.5;
        }

        .istyle {
            width: 100%;
            max-width: 280px;
            border: 1px solid #ccc;
            text-align: center;

            @include border-radius(100px);
        }
    }

    // 투자금 변경
    .investment-change {
        padding: 50px 40px 60px;
        font-size: 20px;
        text-align: center;
        line-height: 1.5;

        strong {
            color: $plumcolor;
        }

        .istyle {
            max-width: 320px;
            margin-top: 40px;
            background: #f9f9f9;
            text-align: center;

            &::placeholder {
                font-size: 16px;
            }
        }
    }

    // 투자 위험 고지
    .investment-risk {
        font-size: 16px;

        &-notice {
            padding: 50px 40px 60px;
            line-height: 1.5;
        }

        &-agree {
            padding: 20px 40px;
            border-top: 1px solid #ededed;
            font-weight: 700;
            text-align: center;

            .istyle {
                width: 100px;
                border-bottom: 2px solid #222;
                vertical-align: middle;
                text-align: center;
                line-height: 36px;

                &::placeholder {
                    opacity: .3;
                }
            }
        }
    }
}

// tablet 768 ~ 1200
@include respond-to(mobiles) {
    // 타이틀
    .layer-pop {
        padding: 0 20px;

        .pop-head {
            min-height: 28px;
            padding: 10px 0;
            border-bottom-width: 1px;
            
            .tit {
                padding: 0 18px;
                font-size: 16px;
                line-height: 1.2;
            }
        }

        .btn-layer-pop-clse {
            right: 12px;
            top: 12px;
            font-size: 24px;
    
            @include size(24px);
        }
    }
}

// mobile 767
@include respond-to(mobiles) {
    // 타이틀
    .layer-pop {
        // 약관
        .terms-wrap {
            padding: 30px 20px;
        }

        // 공통
        .pop-comm {
            .comm-msg {
                padding-bottom: 60px;
                font-size: 16px;
            }
        }

        .pop-footer {
            padding-left: 20px;
            padding-right: 20px;
        }

        // 설정 저장 목록
        .set-list {
            padding-top: 20px;
            padding-bottom: 20px;

            .total {
                margin-bottom: 15px;
                font-size: 16px;
            }
        }

        // 문의하기 비밀글 비밀번호
        .secret-password {
            padding: 30px 30px 40px;

            .t {
                font-size: 16px;
            }
        }

        // 유저 정보 찾기
        .find-user {
            padding: 30px 30px 40px;
        }

        // 투자금 변경
        .investment-change {
            padding: 30px 30px 40px;
            font-size: 16px;
        }
    }
}