// webfont NanumSquareRound
@include font-face(NanumSquareRound, $url-fonts + NanumSquareRoundL, 300, normal, eot woff2 woff ttf);

@include font-face(NanumSquareRound, $url-fonts + NanumSquareRoundR, 400, normal, eot woff2 woff ttf);

@include font-face(NanumSquareRound, $url-fonts + NanumSquareRoundB, 700, normal, eot woff2 woff ttf);

@include font-face(NanumSquareRound, $url-fonts + NanumSquareRoundEB, 800, normal, eot woff2 woff ttf);

body {
    font-family: 'NanumSquareRound', 'Malgun Gothic', 'AppleSDGothicNeo', 'Helvetica', sans-serif;
    color: $basecolor;
    font-weight: 400;
    background: #fff;
    
    @include wordwrap;
}

// common
.blind {
	display: none;
}

// body scroll hidden and modal
.no-scroll {
    overflow: hidden;

    body {
        overflow: scroll;
        
        &:before {
            content: "";
            position: fixed;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.5);
            z-index: 100;
        }
    }
}

.ir {
	visibility: hidden;
	overflow: hidden;
	position: absolute;
	width: 1%;
	height: 1%;
	margin: 0;
	padding: 0;
	font-size: 1%;
	line-height: 1%;
}

.dash {
    position: relative;
    display: inline-block;
    width: 16px;
    height: 1px;
    margin: 0 16px;
    font-size: 0;
    background: #ccc;
    line-height: 0;
    vertical-align: middle;
}

// label
.label {
    display: inline-block;
    padding: 10px 15px;
    font-size: 12px;
    color: #fff;
    vertical-align: top;

    &.black {
        background: #222;
    }

    &.rnd {
        @include border-radius(100px);
    }
}

// responsive
.is-mobile {
    display: none;
}

// layout
#wrap {
    position: relative;
    overflow: hidden;
    min-width: 320px;
    padding-top: 81px;
}

#container {
    min-height: 480px;
}

// 툴팁용 아이콘
.ico-tip {
    display: inline-block;
    position: relative;
    top: -1px;
    background: url($url-images + "/comm/ico_tip.png") no-repeat 0 0;
    margin-left: 10px;
    vertical-align: middle;
    background-size: cover;
    cursor: pointer;

    @include size(20px);
}

// 툴팁용 레이어
.layer-tip {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    min-width: 280px;
    padding: 20px;
    border: 1px solid $basecolor2;
    font-size: 14px;
    background: #fff;
    line-height: 1.3;
    z-index: -9999;

    @include box-sizing;
    @include box-shadow(0 2px 3px 0 rgba(38, 59, 94, 0.1));
    @include transition(opacity .3s);

    &:before {
        content: "";
        position: absolute;
        left: -10px;
        top: 25px;
        display: block;
        width: 20px;
        height: 19px;
        background: url($url-images + "/comm/ico_tip_left.png") no-repeat 0 0;
        background-size: cover;        
    }

    .t {
        display: block;
        margin-bottom: 20px;
        font-size: 24px;
    }

    .emphasis {
        color: $plumcolor;
    }

    .btn-layer-tip-clse {
        position: absolute;
        right: 10px;
        top: 10px;
        border: 0;
        font-size: 24px;
        color: $basecolor2;
        background: transparent;

        @include size(24px);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        z-index: 9;
    }
}

// float myinfo
.float-myinfo {
    // overflow-y: auto;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    // max-height: 80vh;
    z-index: 2;

    .float-myinfo-wrap {
        display: none;
    }
}

// 약관
.terms-wrap-box {
    .h-terms {
        margin-bottom: 15px;
        font-size: 24px;

        &~ .h2-terms {
            margin-top: 50px;        
        }
    }

    .h2-terms {
        margin-bottom: 15px;
        font-size: 22px;

        &~ .h2-terms {
            margin-top: 40px;        
        }
    }

    .abbr {
        display: block;
        margin-top: 5px;
        font-size: 13px;
        color: #999;
    }

    .terms-contents {
        font-size: 16px;
        color: #666;
        line-height: 1.5;

        li {
            margin-top: 8px;

            &:first-child {
                margin-top: 0;
            }
        }

        dd {
            margin-top: 5px;
            padding: 0 10px;
        }

        &.heading {
            margin-bottom: 30px;
        }

        &~ .terms-contents {
            margin-top: 15px;        
        }

        .terms-contents-in {
            margin-left: 15px;
            padding: 10px 0;
        }
    }

    .board {
        th,
        td {
            padding: 10px 5px;
            text-align: center;
            vertical-align: middle;
            line-height: 1.2;
        }

        th {
            border-width: 1px;
            border-style: solid;
            border-color: #cecece #ededed;
            font-size: 14px;
            font-weight: bold;
            background: #f9f9f9;
        }

        td {
            border: 1px solid #ededed;
            font-size: 13px;

            @include wordwrap();
        }

        tr:first-child td {
            border-top: 0;
        }

        &-t {
            display: block;
            margin-bottom: 5px;
        }

        &-list {
            margin: 0 5px;
            text-align: left;
            line-height: 1.4;
        }

        &-in {
            margin-top: 10px;
        }
    }
}

// tablet 768 ~ 1200
@include respond-to(tablet) {
    // layout
    #wrap {
        padding-top: 49px;
    }

    // mobile 일때
    .is-mobile {
        display: block;
    }

    // float myinfo 
    .float-myinfo {
        display: none;
    }

    // 툴팁용 레이어
    .layer-tip {
        min-width: auto;
    
        &:before {
            display: none;
        }
    }
}

// mobile 767
@include respond-to(mobiles) {
    #container {
        min-height: auto;
    }
}