@charset "utf-8";

.plum-user {
    padding-bottom: 200px;

    .user-head {
        background: $plumcolor;
    }

    // 가입
    .section-join {
        .user-join {
            .join-top {
                text-align: center;

                @include default-width(1200px);

                .h2-tit {
                    font-size: 40px;
                }

                .dsc {
                    margin-top: 35px;
                    font-size: 16px;
                }

                .lnk {
                    color: $plumcolor;
                    text-decoration: underline;
                }
            }

            .join-info {
                margin-top: 80px;
                
                @include default-width(780px);
            }

            .info-contents {
                li {
                    padding: 40px 0;
                    border-top: 1px solid #ededed;

                    @include flexbox;
                    @include align-items;

                    &:first-child {
                        border-top: 0;
                    }
                }

                .tit {
                    display: block;
                    margin-right: 60px;
                    font-size: 24px;

                    strong {
                        display: block;
                        line-height: 48px;
                    }

                    @include flex;
                }

                .contents {
                    position: relative;

                    @include flex;

                    .box {
                        @include flexbox;
                        @include align-items(center);
                    }

                    .istyle {
                        font-size: 16px;
                        background: #f9f9f9;
                    }

                    .reason {
                        display: block;
                        margin-top: 24px;
                        color: $plumcolor;
                        text-decoration: underline;
                    }
                }

                .btn-phone-auth {
                    width: 140px;
                    height: 48px;
                    margin-left: 5px;
                    border: 1px solid #ccc;
                    font-size: 16px;
                    background: #fff;
                }
            }

            .join-terms-check {
                margin-top: 60px;
            }

            .info-button {
                margin-top: 40px;
                text-align: center;
            }
        }

        // 가입 인증
        .user-auth {
            text-align: center;

            @include default-width(1200px);

            .h2-tit {
                font-size: 40px;

                strong {
                    color: $plumcolor;
                }
            }

            .dsc {
                margin: 70px 0;
                font-size: 14px;
                line-height: 1.5;
            }
        }
    }

    // 로그인
    .section-login {
        .user-login {
            @include default-width(724px);

            .login {
                margin-top: 40px;
            }
        }
    }
}