@charset "utf-8";

.plum-cscenter {
    padding-bottom: 200px;

    .cscenter-head {
        background: $plumcolor;
    }

    // 자주하는 질문
    .section-faq {
        .faq-wrap {
            @include default-width(1200px);

            .faq-list {
                margin-top: 40px;
            }

            .list-items {
                display: none;

                &.active {
                    display: block;
                }
            }

            .item {
                overflow: hidden;
                position: relative;

                .h3-tit {
                    position: relative;
                    height: 80px;
                    padding: 0 76px 0 30px;
                    border-bottom: 1px solid #ededed;
                    font-size: 16px;
                    line-height: 1.2;
                    cursor: pointer;

                    @include flexbox;
                    @include align-items(center);

                    .letter-first {
                        margin-right: 10px;
                        font-size: 24px;
                        color: $plumcolor;
                    }

                    i {
                        position: absolute;
                        right: 30px;
                        top: auto;
                    }
                }

                .content {
                    display: none;
                    padding: 40px 62px;
                    font-size: 14px;

                    .content-list {
                        margin-top: 10px;
                        line-height: 1.4;

                        &:first-child {
                            margin-top: 0
                        }

                        li {
                            line-height: 1.6;
                        }
                    }
                }

                &.on {
                    background: #f9f9f9;
                }
            }
        }
    }

    // 문의하기
    .section-qna {
        // 쓰기 
        .qna-write {
            @include default-width(1200px);

            .required-item {
                margin-bottom: 8px;
                font-size: 15px;
                color: #666;
                text-align: right;
            }

            .ico-req {
                color: $pred;
            }

            .write-contents {
                border-top: 1px solid #ccc;
                
                li {
                    padding: 40px 0;
                    border-top: 1px solid #ededed;

                    @include flexbox;
                    @include align-items;

                    &:first-child {
                        border-top: 0;
                    }
                }

                .tit {
                    display: block;
                    width: 360px;
                    margin-right: 60px;
                    font-size: 24px;

                    @include flex(none);

                    strong {
                        display: block;
                        line-height: 48px;
                    }
                }

                .col {
                    max-width: 360px;
                }

                .contents {
                    position: relative;

                    @include flex;

                    .box {
                        @include flexbox;

                        .col {
                            margin-left: 60px;

                            @include flex;

                            &:first-child {
                                margin-left: 0;
                            }
                        }
                    }
                }

                .istyle {
                    font-size: 16px;
                    background: #f9f9f9;
                }

                .filebox {
                    width: 100%;
                }

                .txt {
                    display: block;
                    font-size: 16px;
                    color: $plumcolor;
                    line-height: 48px;
                }

                .file-name {
                    position: relative;

                    .name {
                        display: block;
                        margin-right: 30px;
                        font-size: 16px;
                        line-height: 24px;

                        @include truncate;
                    }

                    .btn-file-del {
                        position: absolute;
                        right: 0;
                        top: 0;
                        border: 1px solid #ccc;
                        font-size: 14px;
                        background: #fff;

                        @include size(24px);
                    }

                    .btn-file-preview {
                        height: 24px;
                        padding: 0 5px;
                        border: 0;
                        font-size: 14px;
                        color: #fff;
                        background: #333;
                    }
                }

                .dsc {
                    display: block;
                    margin-top: 20px;
                    font-size: 16px;
                    color: $basecolor1;
                    line-height: 1.4;
                }

                .itext {
                    display: block;
                    width: 100%;
                    border: 0;
                    padding: 15px 10px;
                    font-size: 16px;
                    background: #f9f9f9;
                    line-height: 1.4;

                    @include box-sizing;
                }

                .txt-cnt {
                    margin-top: 10px;
                    text-align: right;
                }
            }

            .write-terms-check {
                margin-top: 60px;
            }
        }

        // 보기
        .qna-view {
            .h2-tit {
                margin-bottom: 28px;
                font-size: 32px;
                font-weight: 800;
                line-height: 1.3;
            }

            .view-contents {
                @include default-width(1200px);
            }

            .contents-info {
                width: 100%;
                border-top: 2px solid $basecolor;
                border-bottom: 2px solid $basecolor;
                table-layout: fixed;

                th {
                    font-size: 16px;
                    text-align: left;

                    span {
                        position: relative;
                        padding-left: 20px;

                        &:before {
                            content: "";
                            position: absolute;
                            left: 0;
                            top: 50%;
                            margin-top: -2px;
                            font-size: 0;
                            background: $basecolor;

                            @include size(4px);
                            @include border-radius(100px);
                        }
                    }
                }

                td {
                    padding-left: 20px;
                    padding-right: 20px;
                    color: $basecolor1;

                    &.contents {
                        padding: 50px 0;
                        line-height: 1.5;
                    }
                }

                th,
                td {
                    padding-top: 17px;
                    padding-bottom: 17px;
                    border-bottom: 1px solid #ededed;
                }
            }

            // 댓글
            .view-comment {
                margin-top: 60px;
                
                @include default-width(1200px);
            }
        }

        // 리스트
        .qna-list {
            @include default-width(1200px);
    
            .board {
                .txt-lft {
                    padding-left: 20px;
                    padding-right: 20px;
                    line-height: 1.4;
                }

                .ico_lock {
                    font-size: 16px;
                    vertical-align: -2px;
                }

                .cmnt-cnt {
                    margin-left: 10px;
                    color: $plumcolor;
                }
            }
    
            .paging {
                margin-top: 25px;
            }

            .qna-button {
                margin-top: 25px;
                padding-top: 40px;
                border-top: 1px solid #ccc;
            }
        }

        .qna-bottom {
            margin-top: 60px;
            text-align: center;
        }
    }
}