@charset "utf-8";

.plum-lease {
    padding-bottom: 200px;
    
    .lease-head {
        background: $plumcolor;
    }

    // 암호화폐 이용안내
    .section-guide {
        .lease-guide {
            padding-bottom: 200px;
            border-bottom: 1px solid $plumcolor;

            @include default-width(1200px);
        }

        .lease-direct {
            padding-top: 180px;

            .direct-top {
                .h3-tit {
                    font-size: 64px;
                    text-align: center;
                    line-height: 1.3;
                }

                @include default-width(1200px);
            }

            .direct-insert {
                position: relative;
                margin-top: 100px;
                text-align: center;

                &:before,
                &:after {
                    content: "";
                    position: absolute;
                    bottom: 29.5px;
                    width: 38%;
                    height: 1px;
                    font-size: 0;
                    line-height: 0;
                    background: #ededed;
                    z-index: -1;
                }

                &:before {
                    left: 0;
                }

                &:after {
                    right: 0;
                }

                .insert-slt {
                    @include flexbox;
                    @include justify-content(center);
                    
                    .slt {
                        width: 100%;
                        height: 48px;
                        margin-right: 60px;
                        font-size: 24px;
                        font-weight: 400;

                        &:last-child {
                            margin-right: 0;
                        }
                    }

                    .istyle {
                        font-size: 24px;

                        &::placeholder {
                            font-size: 24px;
                        }                        
                    }

                    .slt,
                    .istyle {
                        max-width: 360px;
                        border-bottom: 4px solid $basecolor;
                    }
                }

                .btn {
                    margin-top: 160px;
                }
            }

            .direct-result {
                position: relative;
                padding-top: 240px;
                text-align: center;

                @include default-width(1200px);

                &:before {
                    content: "";
                    position: absolute;
                    left: 50%;
                    top: 80px;
                    width: 1px;
                    height: 80px;
                    margin-left: -0.5px;
                    background: $plumcolor;
                }

                .value {
                    display: block;
                    font-size: 40px;
                    line-height: 1.3;

                    .price {
                        color: $plumcolor;                        
                    }
                }

                .btn {
                    margin-top: 60px;
                }
            }
        }
    }

    // 대여
    .section-lease {
        // 상품 등록
        .lease-product-app {
            .application-top {
                position: relative;

                @include default-width(1200px);

                .h2-tit {
                    font-size: 40px;
                    line-height: 1.2;
                }

                .top-info {
                    margin-top: 100px;
                    
                    @include clear;

                    li {
                        float: left;
                        width: calc((100% - 60px) / 2);
                        margin-right: 60px;
                        margin-top: 20px;

                        @include flexbox;
                        @include align-items(center);

                        &:nth-child(even) {
                            margin-right: 0;
                        }

                        &:first-child,
                        &:first-child + li {
                            margin-top: 0;
                        }
                    }

                    .tit {
                        position: relative;
                        width: calc((100% / 240) * 100);
                        padding-left: 22px;
                        font-size: 24px;

                        @include flex(none);
                        @include box-sizing;

                        &:before {
                            content: "";
                            position: absolute;
                            left: 0;
                            top: 50%;
                            background: #222;

                            @include size(6px);
                            @include border-radius(100px);
                            @include translate(0, -50%);
                        }
                    }

                    .slt {
                        width: 100%;
                        height: 48px;
                        border-bottom: 4px solid $basecolor;
                        font-size: 16px;
                    }

                    .istyle {
                        border-bottom: 4px solid $basecolor;
                        font-size: 16px;
                        background: #fff;
                        line-height: 34px;
                    }
                }

                .btn-set-load {
                    position: absolute;
                    right: 24px;
                    top: 0;
                }

                .top-button {
                    margin-top: 40px;
                    text-align: center;
                }
            }

            .application-set {
                margin-top: 80px;
                padding-top: 80px;
                border-top: 8px solid #f4f4f4;

                .set-condition {
                    // @include clear;
                    @include default-width(1200px);
                    @include flexbox;

                    .condition-chart {
                        max-width: 360px;
                        width: 100%;
                        // height: 420px;
                        margin-right: 60px;
    
                        @include box-sizing;
                        @include flex(none);
                    }
    
                    .condition-detail {
                        @include flex;
                    }

                    .cpn-sliders {
                        .sliders-items {
                            min-height: auto;
                            padding: 20px 0;
                            border: 0;
                        }

                        .item {
                            .tit {
                                position: relative;
                                padding-left: 25px;

                                &:before {
                                    content: "";
                                    position: absolute;
                                    left: 0;
                                    top: 50%;
                                    margin-top: -3px;
                                    background: $basecolor;

                                    @include size(6px);
                                    @include border-radius(100px);
                                }
                            }
                        }
                    }

                    .condition-noti {
                        margin-top: 10px;
                        font-size: 15px;
                        color: $basecolor1;
                        line-height: 1.3;
                    }
                }

                .set-date {
                    margin-top: 120px;

                    @include default-width(1200px);
                    @include flexbox;
                    @include align-items(center);

                    .tit {
                        position: relative;
                        width: 240px;
                        padding-left: 22px;
                        font-size: 24px;
                        
                        @include box-sizing;

                        &:before {
                            content: "";
                            position: absolute;
                            left: 0;
                            top: 50%;
                            background: #222;

                            @include size(6px);
                            @include border-radius(100px);
                            @include translate(0, -50%);
                        }
                    }

                    .idate {
                        .istyle {
                            max-width: 160px;
                            padding-right: 34px;
                            border-bottom: 4px solid $basecolor;
                            font-size: 16px;
                            background: url($url-images + "/comm/ico_calendar.png") no-repeat 100% 50%;
                            background-size: 24px auto;
                        }
                    }
                }
            }

            .application-check {
                margin-top: 80px;

                @include default-width(1200px);
            }

            .application-bottom {
                margin-top: 80px;

                .button-group {
                    @include default-width(1200px);
                    @include flexbox;
                    @include justify-content(space-between);
                }

                .group {
                    @include flexbox;
                    @include flex;
                }
            }
        }

        // 상품 등록 신청 완료
        .lease-result {
            @include default-width(1200px);

            .result-top {
                position: relative;
                height: 60px;
                margin-bottom: 15px;
                border-bottom: 2px solid $basecolor;

                .h2-tit {
                    font-size: 32px;
                    font-weight: 800;
                }
            }
        }

        // 필수 고지 항목
        .required-notice {
            margin-top: 80px;
        }

        // 상품 보기
        .lease-search {
            @include default-width(1200px);

            .search-top {
                position: relative;
                margin-bottom: 100px;

                .cpn-sliders {
                    .item {
                        .istyle {                        
                            text-align: left;
                        }
                    }
                }

                .btn-lease-search {
                    position: absolute;
                    right: 0;
                    top: -76px;

                    i {
                        position: relative;
                        top: 1px;
                    }
                }

                .top-button {
                    margin-top: 60px;
                    text-align: center;
                }

                &.clse {
                    margin-bottom: 0;
                }
            }

            .search-result {
                .h3-tit {
                    margin-bottom: 30px;
                    font-size: 24px;

                    span {
                        color: $plumcolor;
                    }
                }

                .paging {
                    margin-top: 25px;
                }
            }
        }

        // 진행상태 그래프
        .funding-graph {
            max-width: 1200px;
            margin: 0 auto 54px;
        }

        // 상품
        .lease-product {
            @include default-width(1200px);

            .product-info {
                margin-top: 40px;

                &:first-child {
                    margin-top: 0;
                }
            }
        }

        // 약관 체크
        .lease-check {
            margin-top: 80px;

            @include default-width(1200px);
        }

        .lease-bottom {
            margin-top: 60px;
            padding: 60px 0;
            background: #f4f4f4;
            text-align: center;

            .button-group {
                @include default-width(1200px);
            }
        }
    }

    .lease-footer {
        margin-top: 120px;
        text-align: center;

        @include default-width(1200px);
    }
}