@charset "utf-8";

.plum-loan {
    padding-bottom: 200px;

    .loan-head {
        background: #ffc033;
    }

    // 대출이란
    .section-guide {
        .loan-guide {
            padding-bottom: 200px;
            border-bottom: 1px solid $plumcolor;

            @include default-width(1200px);
        }

        .loan-direct {
            padding-top: 180px;

            .direct-top {
                .h3-tit {
                    font-size: 64px;
                    text-align: center;
                    line-height: 1.3;
                }

                @include default-width(1200px);
            }

            .direct-insert {
                position: relative;
                margin-top: 100px;
                text-align: center;

                &:before,
                &:after {
                    content: "";
                    position: absolute;
                    bottom: 29.5px;
                    width: 38%;
                    height: 1px;
                    font-size: 0;
                    line-height: 0;
                    background: #ededed;
                    z-index: -1;
                }

                &:before {
                    left: 0;
                }

                &:after {
                    right: 0;
                }

                .insert-slt {
                    @include flexbox;
                    
                    .slt {
                        width: 100%;
                        height: 48px;
                        margin-right: 60px;
                        font-size: 24px;
                        font-weight: 400;
                    }

                    .istyle {
                        font-size: 24px;

                        &::placeholder {
                            font-size: 24px;
                        }                        
                    }

                    .slt,
                    .istyle {
                        border-bottom: 4px solid $basecolor;
                    }
                }

                .btn {
                    margin-top: 160px;
                }
            }

            .direct-result {
                position: relative;
                padding-top: 240px;
                text-align: center;

                @include default-width(1200px);

                &:before {
                    content: "";
                    position: absolute;
                    left: 50%;
                    top: 80px;
                    width: 1px;
                    height: 80px;
                    margin-left: -0.5px;
                    background: $plumcolor;
                }

                .value {
                    display: block;
                    font-size: 40px;

                    .price {
                        color: $plumcolor;                        
                    }
                }

                .estimated {
                    margin-top: 30px;
                    font-size: 24px;
                    line-height: 1.6;

                    strong {
                        color: $plumcolor;
                    }
                }

                .btn {
                    margin-top: 60px;
                }
            }

            .direct-guide {
                margin-top: 80px;
                padding: 60px 0;
                font-size: 16px;
                background: #fafbfc;
                text-align: center;

                .box {
                    @include default-width(1200px);
                }

                .h4-tit {
                    font-size: 24px;
                }

                .dsc {
                    margin-top: 20px;
                }

                .list {
                    margin-top: 25px;

                    li {
                        margin-top: 10px;
                    }
                }
            }
        }
    }

    // 대출 신청
    .section-loan {
        .loan-step {
            @include default-width(1200px);

            .step-top {
                text-align: center;

                .h2-tit {
                    font-size: 40px;
                }

                .dsc {
                    margin-top: 30px;
                    font-size: 16px;
                }
            }

            .step-info {
                margin-top: 80px;

                .required-item {
                    margin-bottom: 8px;
                    font-size: 15px;
                    color: $basecolor1;
                    text-align: right;
                }

                .ico-req {
                    color: $pred;
                }

                .info-list {
                    border-top: 1px solid #ccc;

                    li {
                        padding: 40px 0;
                        border-bottom: 1px solid #ededed;

                        @include flexbox;
                    }

                    .item {
                        width: calc(100% - 420px);

                        @include flex;

                        .box {
                            @include flexbox;
                        }

                        .col {
                            max-width: 360px;
                            margin-left: 60px;
                            
                            @include flex;

                            &:first-child {
                                margin-left: 0;
                            }
                        }

                        &.tit {
                            width: 360px;
                            margin-right: 60px;
                            font-size: 24px;
                            line-height: 48px;
                            white-space: nowrap;

                            @include flex(none);
                        }

                        &:last-child {
                            margin-right: 0;
                        }
                    }

                    .slt {
                        width: 100%;
                        height: 48px;
                        border-bottom: 4px solid $basecolor;
                        font-size: 16px;
                    }

                    .istyle {
                        font-size: 16px;
                        background: #f9f9f9;
                    }

                    .filebox {
                        width: 100%;
                    }

                    .dsc {
                        display: block;
                        margin-top: 20px;
                        font-size: 16px;
                        color: $basecolor1;
                        line-height: 1.4;
                    }

                    .btn-adrs {
                        @include border-radius(0);
                    }

                    .txt {
                        display: block;
                        font-size: 16px;
                        color: $plumcolor;
                        line-height: 48px;
                    }

                    .file-name {
                        position: relative;

                        .name {
                            display: block;
                            margin-right: 30px;
                            font-size: 16px;
                            line-height: 24px;

                            @include truncate;
                        }

                        .btn-file-del {
                            position: absolute;
                            right: 0;
                            top: 0;
                            border: 1px solid #ccc;
                            font-size: 14px;
                            background: #fff;

                            @include size(24px);
                        }

                        .btn-file-preview {
                            height: 24px;
                            padding: 0 5px;
                            border: 0;
                            font-size: 14px;
                            color: #fff;
                            background: #333;
                        }
                    }
                }
            }

            .step-bottom {
                margin-top: 60px;
                text-align: center;
            }
        }

        .loan-application {
            .application-top {
                @include clear;
                @include default-width(1200px);

                .h2-tit {
                    float: left;
                    width: 50%;
                    font-size: 40px;
                    line-height: 1.2;
                }

                .guarantee-slt {
                    overflow: hidden;

                    li {
                        margin-top: 20px;

                        @include flexbox;
                        @include align-items(center);

                        &:first-child {
                            margin-top: 0;
                        }
                    }

                    .tit {
                        position: relative;
                        width: 240px;
                        padding-left: 22px;
                        font-size: 24px;

                        @include flex(none);
                        @include box-sizing;

                        &:before {
                            content: "";
                            position: absolute;
                            left: 0;
                            top: 50%;
                            background: #222;

                            @include size(6px);
                            @include border-radius(100px);
                            @include translate(0, -50%);
                        }
                    }

                    .slt {
                        width: 100%;
                        height: 48px;
                        border-bottom: 4px solid $basecolor;
                        font-size: 16px;
                    }

                    .istyle {
                        border-bottom: 4px solid $basecolor;
                        font-size: 16px;
                        background: #fff;
                        line-height: 34px;
                    }
                }

                .top-button {
                    margin-top: 40px;
                    text-align: center;

                    @include default-width(1200px);
                }
            }

            .application-set {
                margin-top: 80px;
                padding-top: 80px;
                border-top: 8px solid #f4f4f4;

                .set-condition {
                    // @include clear;
                    @include default-width(1200px);
                    @include flexbox;

                    .condition-chart {
                        max-width: 360px;
                        width: 100%;
                        // height: 420px;
                        margin-right: 60px;
    
                        @include box-sizing;
                        @include flex(none);
                    }
    
                    .condition-detail {
                        @include flex;
                    }

                    .cpn-sliders {
                        .sliders-items {
                            min-height: auto;
                            padding: 20px 0;
                            border: 0;
                        }

                        .item {
                            .tit {
                                position: relative;
                                padding-left: 25px;

                                &:before {
                                    content: "";
                                    position: absolute;
                                    left: 0;
                                    top: 50%;
                                    margin-top: -3px;
                                    background: $basecolor;

                                    @include size(6px);
                                    @include border-radius(100px);
                                }
                            }
                        }
                    }
                }

                .set-result {
                    margin-top: 70px;
                    padding: 80px 0;
                    border-top: 1px solid #ededed;
                    text-align: center;

                    @include default-width(1200px);

                    .value {
                        display: block;
                        font-size: 40px;

                        .price {
                            color: $plumcolor;                        
                        }
                    }

                    .estimated {
                        margin-top: 30px;
                        font-size: 24px;
                        line-height: 1.6;

                        strong {
                            color: $plumcolor;
                        }
                    }
                }
            }

            .application-etc {
                padding-top: 100px;
                border-top: 8px solid #f4f4f4;

                .etc-info {
                    @include clear;
                    @include default-width(1200px);
                }

                .h2-tit {
                    float: left;
                    width: 50%;
                    font-size: 40px;
                    line-height: 1.2;
                }

                .etc-slt {
                    overflow: hidden;

                    > li {
                        margin-top: 20px;

                        @include flexbox;
                        @include align-items(center);

                        &.algin-top {
                            @include align-items(flex-start);
                        }

                        &:first-child {
                            margin-top: 0;
                        }
                    }

                    .tit {
                        position: relative;
                        width: 240px;
                        padding-left: 22px;
                        font-size: 24px;
                        line-height: 48px;

                        @include flex(none);
                        @include box-sizing;

                        &:before {
                            content: "";
                            position: absolute;
                            left: 0;
                            top: 50%;
                            background: #222;

                            @include size(6px);
                            @include border-radius(100px);
                            @include translate(0, -50%);
                        }
                    }

                    .istyle {
                        font-size: 16px;
                        background: #f9f9f9;
                    }

                    .igroup {
                        @include clear;

                        li {
                            float: left;
                            margin-right: 25px;
                            padding: 5px 0;

                            label {
                                font-size: 16px;
                            }

                            &:last-child {
                                margin-right: 0;
                            }
                        }
                    }

                    .itext {
                        display: block;
                        width: 100%;
                        border: 0;
                        padding: 15px 10px;
                        font-size: 16px;
                        background: #f9f9f9;
                        line-height: 1.4;

                        @include box-sizing;
                    }
                }
            }

            .application-check {
                margin-top: 80px;

                @include default-width(1200px);
            }

            .application-bottom {
                margin-top: 80px;
                text-align: center;

                .button-group {
                    @include default-width(1200px);
                }
            }
        }

        // 투자신청 완료
        .loan-result {
            @include default-width(1200px);

            .result-top {
                position: relative;
                height: 60px;
                margin-bottom: 15px;
                border-bottom: 2px solid $basecolor;

                .h2-tit {
                    font-size: 32px;
                    font-weight: 800;
                }
            }
        }

        // 필수 고지 항목
        .required-notice {
            margin-top: 80px;
        }
    }

    .loan-footer {
        margin-top: 120px;
        text-align: center;

        @include default-width(1200px);
    }
}