@charset "utf-8";

.plum-invest {
    padding-bottom: 200px;

    .invest-head {
        background: $plumcolor;
    }
    
    // 투자란
    .section-guide {
        .invest-guide {
            @include default-width(1200px);
        }

        .invest-direct {
            margin-top: 200px;

            .direct-top {
                .h2-tit {
                    padding: 25px 0;
                    border-top: 1px solid #333;
                    border-bottom: 1px solid #333;
                    font-size: 30px;
                    text-align: center;
                }
    
                .h3-tit {
                    margin-top: 190px;
                    font-size: 64px;
                    text-align: center;
                    line-height: 1.3;
                }
                
                @include default-width(1200px);
            }

            .direct-insert {
                margin-top: 100px;
                font-size: 20px;
                text-align: center;

                span {
                    display: inline-block;
                    border-bottom: 4px solid #222;
                    vertical-align: middle;
                }

                .istyle {
                    max-width: 360px;
                }
            }

            .direct-product {
                margin-top: 70px;
                background: #fafbfc;

                .product-items {
                    padding: 80px 0;

                    @include default-width(1200px);
                    @include flexbox;

                    li {
                        margin-right: 60px;
                        padding: 55px 40px;
                        text-align: center;

                        @include flex;
                        @include border-radius(24px);

                        &.basic {
                            background: #dfefee;
                        }

                        &.type1 {
                            background: #f0ebe7;
                        }

                        &.type2 {
                            background: #f0e9f2;
                        }

                        .special-contract {
                            position: absolute;
                        }

                        &:last-child {
                            margin-right: 0;
                        }
                    }

                    .h4-tit {
                        font-size: 40px;
                    }

                    .dsc {
                        position: relative;
                        height: 125px;
                        margin-top: 40px;
                        font-size: 20px;
                        color: $basecolor1;
                        line-height: 1.3;

                        &:after {
                            content: "";
                            position: absolute;
                            left: 50%;
                            bottom: 0;
                            width: 40px;
                            height: 1px;
                            font-size: 0;
                            line-height: 0;
                            background: $basecolor;

                            @include translate(-50%, 0);
                        }
                    }

                    .info {
                        margin: 35px 0 40px;

                        .revenue {
                            display: block;
                            margin-bottom: 10px;
                            font-size: 24px;

                            .rate {
                                display: block;
                                margin-top: 15px;
                                color: $plumcolor;
                            }
                        }

                        .txt {
                            font-size: 16px;
                            color: $basecolor1;
                        }
                    }

                    // 특약상품 툴팁
                    .special-contract {
                        visibility: hidden;
                        opacity: 0;
                        position: absolute;
                        left: 0;
                        top: 26px;
                        width: 100%;
                        padding: 20px 30px;
                        border: 1px solid #ddd;
                        font-size: 15px;
                        color: $basecolor1;
                        background: #fff;
                        text-align: left;
                        line-height: 1.3;
                        z-index: -9999;

                        @include box-sizing;

                        .t {
                            color: $basecolor;
                        }

                        .cont {
                            margin-top: 10px;
                        }

                        @include border-radius(24px);
                    }

                    .layer-refer {
                        color: $basecolor;

                        &:hover ~ .special-contract {
                            visibility: visible;
                            opacity: 1;
                            z-index: 1;
                        }
                    }

                    .btn-product {
                        display: inline-block;
                        width: 184px;
                        height: 56px;
                        font-size: 20px;
                        font-weight: 700;
                        background: #fff;
                        line-height: 56px;
                        vertical-align: top;
                    }
                }
            }
        }
    }

    // 투자하기
    .section-invest {
        // 투자 상품 검색
        .invest-search {
            @include default-width(1200px);

            .search-top {
                .search-type {
                    @include flexbox;

                    li {
                        margin-right: 60px;

                        @include flex;
                        
                        &:last-child {
                            margin-right: 0;
                        }
                    }

                    .btn-label {
                        display: block;
                        height: 80px;
                        border: 1px solid #ccc;
                        font-size: 32px;
                        font-weight: 700;
                        background: #fff;
                        line-height: 78px;
                        text-align: center;

                        @include border-radius(24px);

                        &:hover {
                            border: 0;
                            background: #cde5e3;
                            line-height: 80px;
                        }
                    }

                    .icheck {
                        opacity: 0;
                        position: absolute;
                        z-index: -9999;

                        &:checked ~ .btn-label {
                            border: 0;
                            background: #cde5e3;
                            line-height: 80px;
                        }
                    }
                }

                .search-detail {
                    margin-top: 40px;
                }

                .top-button {
                    margin-top: 60px;
                    text-align: center;
                }
            }

            .search-result {
                margin-top: 100px;
                
                .h3-tit {
                    margin-bottom: 30px;
                    font-size: 24px;

                    span {
                        color: $plumcolor;
                    }
                }

                .paging {
                    margin-top: 25px;
                }

                .search-result-bottom {
                    margin-top: 24px;
                    padding-top: 38px;
                    border-top: 2px solid $plumcolor;
                    text-align: center;

                    .button-group {
                        @include flexbox;
                        @include justify-content(space-between);
                    }
                }
            }
        }

        // 진행상태 그래프
        .funding-graph {
            max-width: 1200px;
            margin: 0 auto 54px;
        }

        // 투자 상품
        .invest-product {
            @include default-width(1200px);

            .invest-info {
                @include clear;
            }

            .product-info {
                float: left;
                width: calc((100% / 3) * 2);
                margin-right: 60px;
            }

            .product-revenue {
                overflow: hidden;
                width: calc((100% / 3) - 60px);
            }

            .exp {
                display: block;
                margin-top: 50px;
                font-weight: 700;
            }

            .slt-wrap {
                font-size: 24px;
                font-weight: 700;

                @include flexbox;
                @include align-items(center);

                .slt,
                .istyle {
                    height: 40px;
                    border-bottom: 4px solid $basecolor;
                    font-size: 24px;
                    line-height: 26px;

                    @include flex;
                }

                .slt {
                    font-weight: 800;
                }

                .istyle {
                    margin-left: 4px;
                    font-weight: 800;
                }
            }

            .estimated-revenue {
                margin-top: 30px;
                font-size: 32px;
                line-height: 1.3;

                .revenue-info {
                    margin: 12px 0;
                    padding: 20px 0;
                    border-top: 1px solid #dedede;
                    border-bottom: 1px solid #dedede;
                    font-size: 24px;
                    line-height: 1;
                }

                .revenue-sum {
                    position: relative;

                    .t {
                        font-weight: 800;
                    }

                    .price {
                        position: absolute;
                        right: 0;
                        font-style: normal;
                        color: $plumcolor;
                    }
                }

                .revenue-item {
                    margin-top: 25px;
                    font-size: 20px;

                    li {
                        position: relative;
                        margin-top: 15px;

                        .price {
                            position: absolute;
                            right: 0;
                        }

                        &:first-child {
                            margin-top: 0;
                        }
                    }
                }
            }

            // 지급 스케줄
            .revenue-schedule {
                margin-top: 20px;
                font-size: initial;
                text-align: right;
                line-height: 1;

                .btn-revenue-schedule {
                    border: 0;
                    font-size: 16px;
                    text-decoration: underline;
                    background: none;
                }
            }
        }

        // 예상 지급 스케줄
        .payments-schedule {
            margin-top: 40px;
        }

        .invest-check {
            margin-top: 80px;

            @include default-width(1200px);
        }

        .invest-button {
            margin-top: 60px;
            padding: 60px 0;
            background: #f4f4f4;
            text-align: center;

            .button-group {
                @include default-width(1200px);
            }
        }

        // 대출자 정보
        .invest-lender {
            margin-top: 60px;

            @include default-width(1200px);
        }

        // 투자신청 완료
        .invest-result {
            @include default-width(1200px);

            .result-top {
                position: relative;
                height: 60px;
                margin-bottom: 15px;
                border-bottom: 2px solid $basecolor;

                .h2-tit {
                    font-size: 32px;
                    font-weight: 800;
                }

                .purpose {
                    position: absolute;
                    top: 0;
                    right: 0;
                    font-size: 24px;
                    font-weight: 700;
                    line-height: 32px;
                }
            }
        }
    }

    // 자동 투자
    .section-auto-invest {
        .auto-invest-top {
            @include default-width(1200px);

            .h2-tit {
                height: 60px;
                border-bottom: 2px solid $basecolor;
                font-size: 32px;
                font-weight: 800;
            }
        }

        .auto-invest-info {
            margin-top: 80px;

            @include clear;

            > li {
                float: left;
                width: 100%;
                margin-bottom: 60px;

                @include flexbox;
                @include align-items(center);

                .tit {
                    position: relative;
                    width: 100%;
                    max-width: 220px;
                    padding-left: 25px;
                    font-size: 24px;

                    // @include flex;

                    &:before {
                        content: "";
                        position: absolute;
                        left: 0;
                        top: 50%;
                        margin-top: -3px;
                        background: $basecolor;

                        @include size(6px);
                        @include border-radius(100px);
                    }
                }

                .conts {
                    @include flex;
                }

                .igroup {
                    @include clear;

                    li {
                        float: left;
                        margin-right: 30px;

                        &:last-child {
                            margin-right: 0;
                        }
                    }
                }

                .istyle {
                    background: #f9f9f9;
                }

                .slt {
                    height: 48px;
                    border-bottom: 4px solid $basecolor;
                    font-size: 16px;
                }

                &.col {
                    width: calc(50% - 30px);

                    & + .col {
                        margin-left: 60px;
                    }

                    &:first-child {
                        margin-left: 0;
                    }
                }

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }

        .auto-invest-set {
            margin-top: 80px;
            padding-top: 80px;
            border-top: 8px solid #f4f4f4;

            .set-condition {
                @include default-width(1200px);    
                @include flexbox;
                
                .condition-chart {
                    position: relative;
                    max-width: 360px;
                    width: 100%;
                    // height: 420px;
                    margin-right: 60px;

                    @include box-sizing;
                    @include flex(none);

                    .label {
                        position: absolute;
                        left: 0;
                        top: 0;
                    }
                }

                .condition-detail {
                    @include flex;
                }

                .cpn-sliders {
                    .sliders-items {
                        min-height: auto;
                        padding: 20px 0;
                        border: 0;
                    }

                    .item {
                        .tit {
                            position: relative;
                            padding-left: 25px;

                            &:before {
                                content: "";
                                position: absolute;
                                left: 0;
                                top: 50%;
                                margin-top: -3px;
                                background: $basecolor;

                                @include size(6px);
                                @include border-radius(100px);
                            }
                        }
                    }

                    .igroup {
                        li {
                            margin-right: 20px;

                            label {
                                font-size: 16px;
                            }

                            &:last-child {
                                margin-right: 0;
                            }
                        }
                    }
                }
            }
        }

        .auto-invest-check {
            margin-top: 80px;
            
            @include default-width(1200px);
        }

        .auto-invest-bottom {
            margin-top: 80px;

            @include default-width(1200px);

            .button-group {
                text-align: center;
                // @include flexbox;
                // @include justify-content(space-between);
            }
        }

        // 투자신청 완료
        .auto-invest-result {
            @include default-width(1200px);

            .result-top {
                position: relative;
                height: 60px;
                margin-bottom: 15px;
                border-bottom: 2px solid $basecolor;

                .h2-tit {
                    font-size: 32px;
                    font-weight: 800;
                }

                .purpose {
                    position: absolute;
                    top: 0;
                    right: 0;
                    font-size: 24px;
                    font-weight: 700;
                    line-height: 32px;
                }
            }

            // 필수 고지 항목
            .required-notice {
                margin-top: 80px;
            }

            .result-button {
                margin-top: 80px;
                text-align: center;
        
                @include default-width(1200px);
                
                .btn  {
                    width: 100%;
                    max-width: 280px;
                }
            }
        }
    }

    .invest-footer {
        margin-top: 120px;
        text-align: center;

        @include default-width(1200px);
    }
}