@charset "utf-8";

// button
.btn {
    width: 100%;
    text-align: center;
    white-space: nowrap;

	@include box-sizing;
    @include border-radius(200px);
    @include transition(all .3s);

    // button 이외 태그
    &-inline {
        @include inline-flex;
        @include align-items(center);
        @include justify-content(center);
    }
    
    // size
    &-small {
        height: 36px;
        padding-left: 15px;
        padding-right: 15px;
        font-size: 14px;
        font-weight: 700;

        &.btn-border {
            padding-left: 14px;
            padding-right: 14px;
        }
    }

    &-medium {
        width: 160px;
        height: 48px;
        padding-left: 30px;
        padding-right: 30px;
        font-size: 16px;
        font-weight: 700;

        &.btn-border {
            padding-left: 29px;
            padding-right: 29px;
        }
    }

    &-xlarge {
        max-width: 280px;
        height: 60px;
        padding-left: 36px;
        padding-right: 36px;
        font-size: 20px;
        font-weight: 800;

        &.btn-border {
            padding-left: 35px;
            padding-right: 35px;
        }
    }
    
    // color
    &-pupple {
        border: 0;
        color: #fff;
        background: $plumcolor;

        &:hover {
            background: #7624c7;
        }

        &.btn-border {
            border: 1px solid $plumcolor;
            color: $basecolor;
            background: #fff;

            &:hover {
                color: $plumcolor;
            }
        }
    }

    &-lgray {
        border: 0;
        background: $lgray;

        &:hover {
            background: #cecece;
        }

        &.btn-border {
            border: 1px solid #ccc;
        }
    }

    &-white {
        border: 0;
        background: #fff;

        &:hover {
            background: #f9f9f9;
        }

        &.btn-border {
            border: 1px solid #ccc;
        }
    }
}
.button-group {
    font-size: 0;

    .btn {
        margin-left: 10px;

        &:first-child {
            margin-left: 0;
        }
    }
}

// form
.istyle {
    width: 100%;
    height: 48px;
    border: 0;
    padding: 5px 10px;
    font-size: 20px;
    line-height: 38px;

    @include box-sizing;

    &.fail {
        border: 1px solid $pred !important;
    }
}

// select
.slt {
    padding: 5px 6px;
    border: 0;
    background: transparent;
    outline: 0;
}

// 라디오 버튼
.iradiobox {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    z-index: -9999;

    & + label {
        position: relative;
        display: inline-block;
        padding-left: 34px;
        font-weight: 700;
        vertical-align: middle;

        &:before {
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            margin-top: -8px;
            border: 1px solid #ccc;
            font-size: 0;
            line-height: 0;

            @include size(16px);
            @include box-sizing;
            @include border-radius(100px);
        }
    }

    &:checked + label {
        &:before {
            border: 0;
            background: $plumcolor;
        }
    }
}

// 체크박스
.icheckbox {
    position: relative;
    display: inline-block;
    vertical-align: top;

    label {
        position: relative;
        display: inline-block;
        min-width: 16px;
        min-height: 16px;
        padding-left: 34px;
        vertical-align: top;

        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            border: 1px solid #ccc;
            font-size: 0;
            background-color: #fff;
            line-height: 0;
    
            @include size(16px);
            @include box-sizing;
        }

        i {
            visibility: hidden;
            position: absolute;
            left: 0;
            top: 0;
            font-size: 16px;
            font-weight: 700;
            color: $plumcolor;
        }
    }

    [type=checkbox] {
        visibility: hidden;
        opacity: 0;
        position: absolute;
        z-index: -9999;

        &:checked + label {
            &:before {
                border-color: $plumcolor;
            }

            i {
                visibility: visible;
            }
        }
    }

    &.nottxt {
        label {
            padding-left: 0;
        }
    }
}

// 체크박스 스위치 버튼
.icheck-switch {
    position: relative;
    display: inline-block;

    [type=checkbox] {
        opacity: 0;
        position: absolute;
        top: 0;
        left: 0;

        &:checked {
            ~ label {
                .icheck-switch-in {
                    background: $plumcolor;

                    &:before {
                        content: attr(data-unchecked);
                        left: 2px;
                    }

                    &:after {
                        content: attr(data-checked);
                        color: $plumcolor;
                        font-weight: 600;

                        @include transform(translate3d(28px, 0, 0));
                    }
                }
            }
        }
    }

    label {
        position: relative;
        display: inline-block;
        font-size: 0;
        line-height: 0;
    }

    .icheck-switch-in {
        position: relative;
        display: inline-block;
        width: 60px;
        height: 32px;
        border-radius: 100px;
        background: rgba(0,0,0,.4);

        @include transition(background-color 0.3s cubic-bezier(0, 1, 0.5, 1));

        &:before {
            content: attr(data-checked);
            overflow: hidden;
            position: absolute;
            top: 2px;
            left: 30px;
            width: 28px;
            height: 28px;
            font-size: 11px;
            color: rgba(255, 255, 255, 0.5);
            line-height: 28px;
            text-align: center;
            text-indent: -9999px;

            @include box-sizing();
        }

        &:after {
            content: attr(data-unchecked);
            overflow: hidden;
            position: absolute;
            top: 2px;
            left: 2px;
            width: 28px;
            height: 28px;
            border-radius: 100px;
            font-size: 11px;
            color: #777;
            background: #fff;
            line-height: 28px;
            text-align: center;
            z-index: 1;
            text-indent: -9999px;
            
            @include box-sizing();
            @include transition(transform 0.3s cubic-bezier(0, 1, 0.5, 1));
            @include transform(translate3d(0, 0, 0));
        }
    }
}

// 확인 메시지
.comm-msg-form {
    padding-top: 15px;

    &.fail {
        color: $pred;
    }
}

// 테이블
.board-wrap {
    .board {
        width: 100%;

        // 높은순 낮은순 정렬 삼각형
        .sort {
            display: inline-block;
            vertical-align: top;
            cursor: pointer;
        }

        .ico-sort {
            display: inline-block;
            font-size: 0;
            background: url($url-images + "/comm/ico_full_updown.png") no-repeat 50% 50%;
            background-size: 82%;
            vertical-align: -4px;

            @include size(16px);

            &.up {
                background-image: url($url-images + "/comm/ico_full_up.png");
                background-size: cover;
            }

            &.down {
                background-image: url($url-images + "/comm/ico_full_down.png");
                background-size: cover;
            }
        }

        thead {
            th {
                background: $plumcolor;
            }
        }

        th {
            height: 80px;
            font-size: 14px;
            color: #fff;
        }

        td {
            height: 80px;
            border-bottom: 1px solid #ededed;
            font-size: 14px;
            text-align: center;
        }

        // 왼쪽 정렬
        .txt-lft {
            text-align: left;
        }

        // 데이타 없음
        .not-data {
            padding: 50px 0;
            font-size: 24px;
            font-weight: 700;
            text-align: center;
        }

        // 링크
        .alink {
            text-decoration: underline;
        }

        // 셀렉트
        .basic {
            font-size: 14px;
        }

        // 가로 리니어 차트
        .chart-hor {
            height: 66px;
        }

        // 거래불가 상품
        .untradeable {
            td {
                background-color: rgba(142, 41, 244, 0.1);
            }

            &:hover {
                td {
                    background-color: rgba(142, 41, 244, 0.2);
                }
            }
        }

        // tr에 링크 있을때
        &.link {
            tbody {
                tr:hover {
                    background: #f9f9f9;;
                    cursor: pointer;
                }
            }
        }
    }
}

// paging
.cpn-paging {
    font-size: 0;
    text-align: center;

    a {
        display: inline-block;
        font-size: 14px;
        text-align: center;
        line-height: 32px;
        vertical-align: middle;

        @include size(32px);

        &.active {
            background: #ffcb57;

            @include border-radius(100px);
        }
    }

    [class^=paging-] {
        background: {
            position: 50% 50%;
            repeat: no-repeat;
        }
    }

    .paging {
        &-first {
            margin-right: 60px;
            background-image: url($url-images + "comm/img_paging_first.png");
        }
        &-prev {
            margin-right: 20px;
            background-image: url($url-images + "comm/img_paging_prev.png");
        }
        &-next {
            margin-left: 20px;
            background-image: url($url-images + "comm/img_paging_next.png");
        }
        &-last {
            margin-left: 60px;
            background-image: url($url-images + "comm/img_paging_last.png");
        }
    }
}

// 슬라이드바
.cpn-sliders {
    .sliders-items {
        min-height: 96px;
        padding: 24px 0;
        border-top: 1px solid #ededed;

        @include flexbox;
        @include align-items(center);
        @include box-sizing;
        // @include justify-content(space-between);

        &.between {
            @include justify-content(space-between);
        }

        &:last-child {
            border-bottom: 1px solid #ededed;
        }
    }

    .item {
        position: relative;
        margin-right: 60px;

        // @include flex(0, 0, calc((100% - 120px) / 3));
        @include flex;

        &.col {
            @include flex(0, 0, calc((100% - 120px) / 3));
        }

        // insert
        &.ivalue {
            max-width: 120px;

            &.multi {
                max-width: none;

                @include flexbox;
                @include align-items(center);
        
                .dash {
                    @include flex(none);
                }
            }
        }

        .tit {
            font-size: 24px;
        }

        .istyle {
            height: 48px;
            font-size: 16px;
            font-weight: 700;
            text-align: right;
            background: #f9f9f9;
        }

        .slt {
            width: 100%;
            height: 48px;
            border-bottom: 4px solid $basecolor;
            font-size: 16px;
        }

        &:last-child {
            margin-right: 0;
        }
    }

    // radio or checkbox
    .igroup {
        @include clear;

        li {
            float: left;
            margin-right: 55px;
            padding: 5px 0;

            label {
                font-size: 24px;
            }

            &:last-child {
                margin-right: 0;
            }
        }
    }
}

// 문의하기
.cpn-question {
    .question-box {
        position: relative;
        height: 410px;
        padding-top: 110px;

        @include default-width(1200px);

        padding-left: 0;
        padding-right: 0;

        &:after {
            content: "";
            position: absolute;
            right: 0;
            bottom: 0;
            width: 803px;
            height: 545px;
            background: url($url-images + "/comm/qa_bg.png") no-repeat 0 0;
            background-size: cover;
        }
    }

    .h1-tit {
        position: relative;
        margin-bottom: 40px;
        font-size: 52px;
        font-weight: 800;
        line-height: 1.2;
        z-index: 1;
    }

    .btn {
        position: relative;
        z-index: 1;
    }
}

// float myinfo
.cpn-float-myinfo {
    position: relative;

    .float-myinfo-wrap {
        position: relative;
        border-top: 1px solid #ccc;
        background-color: #f7f7f7;
    }

    .cpn-wrap {
        @include default-width(1600px);
    }

    .myinfo-login {
        position: relative;
        height: 320px;

        @include flexbox;
        @include align-items(center);
        @include box-sizing;

        .tit {
            font-size: 40px;
        }

        .dsc {
            margin-top: 25px;
            font-size: 16px;
        }

        .btn-myinfo-login {
            position: absolute;
            right: 0;
        }
    }

    .myinfo-summary {
        padding: 60px 0;

        @include flexbox;
        @include align-items(center);

        .summary-category {
            margin-right: 40px;
            border-right: 1px solid #fff;

            @include flex;
            @include flexbox;

            > li {
                padding: 10px 40px;
                border-left: 1px solid #fff;
                border-right: 1px solid #e6e7e8;

                @include box-sizing;
                @include flex;

                &:first-child {
                    padding-left: 0;
                    border-left: 0;
                }
            }

            .subject {
                font-size: 24px;
                color: $plumcolor;
            }
        }

        .summary-cnt {
            width: 320px;

            @include flex(none);

            > li {
                margin-top: 35px;
                font-size: 16px;

                @include flexbox;
                @include align-items(center);
                @include justify-content(space-between);

                &:first-child {
                    margin-top: 0;
                }
            }

            .t {
                font-weight: 700;
            }

            .cnt-info {
                @include clear;

                li {
                    float: left;
                    margin-left: 5px;

                    span {
                        color: $plumcolor;
                    }

                    &:first-child {
                        margin-left: 0;
                    }
                }
            }
        }

        .category-info {
            margin-top: 20px;

            li {
                margin-top: 40px;

                &:first-child {
                    margin-top: 0;
                }
            }

            .t {
                font-size: 20px;
            }

            .sum {
                display: block;
                margin-top: 15px;
                font-size: 32px;

                span {
                    font-size: 20px;
                    font-weight: normal;
                }

                .rate {
                    font-size: 20px;
                }

                .plus {
                    color: $plus;
                }

                .minus {
                    color: $minus;
                }
            }
        }
    }

    .float-myinfo-btn {
        position: absolute;
        left: 50%;
        top: 0;
        width: 100%;
        max-width: 1600px;
        text-align: right;

        @include translate(-50%, 0);        
    }

    .btn-float-myinfo {
        position: absolute;
        right: 0;
        bottom: -1px;
        width: 92px;
        height: 32px;
        font-size: 12px;
        background-color: #f7f7f7;

        @include border-style(1px, 1px, 0, 1px, solid, #ccc);
    }
}

// section head
.cpn-section-head {
    height: 320px;
    margin-bottom: 80px;
    padding-top: 140px;
    color: #fff;
    text-align: center;

    @include box-sizing;

    .box {
        @include default-width(1200px);
    }

    .h1-tit {
        font-size: 64px;
    }

    .dsc {
        margin-top: 25px;
        font-size: 24px;
        line-height: 1.3;
    }

    // 상품일때
    &.product-head {
        height: 240px;
        margin-bottom: 60px;
        padding-top: 150px;
        text-align: left;

        .box {
            position: relative;
        }

        .h1-tit {
            font-size: 52px;

            span {
                margin-left: 30px;
                font-size: 12px;
                color: rgba(255, 255, 255, 0.5);
            }
        }

        .dsc {
            position: absolute;
            right: 24px;
            top: 50%;
            margin-top: 0;
            font-weight: 700;
            line-height: 1;

            @include translate(0, -50%);

            span {
                display: block;
                margin-bottom: 6px;
                font-size: 12px;
                color: rgba(255, 255, 255, 0.5);
            }
        }
    }

    // 결과
    &.result-head {
        height: 240px;
        margin-bottom: 120px;
        padding-top: 110px;

        .h1-tit {
            font-size: 40px;
        }

        .dsc {
            font-size: 16px;
        }
    }

    // 검색
    &.search-head {
        height: 420px;
        
        .search {
            max-width: 780px;
            margin: 50px auto 0;
    
            .istyle {
                height: 70px;
                padding-left: 64px;
                font-size: 24px;
                background: #fff url($url-images + "/comm/ico_search.png") no-repeat 24px 50%;
                background-size: 24px;
                line-height: 60px;
    
                &:focus {
                    background-image: url($url-images + "/comm/ico_search_on.png");
                }
            }
        }
    }
}

// 리니어 그래프
.cpn-graph {
    .graph-box {
        height: 52px;
        background: #f9f9f9;
    }

    .bar {
        display: inline-block;
        height: 52px;
        font-size: 16px;
        font-weight: 700;
        background: #8be017;
        vertical-align: top;
        line-height: 52px;
        text-align: center;
    }

    .graph-amount {
        margin-top: 14px;
        font-size: 16px;
        color: $basecolor1;
        text-align: right;

        strong {
            color: $basecolor;
        }
    }
}

// 상품 정보
.cpn-product-info {
    @include flexbox;

    .info-items {
        margin-right: 60px;

        @include flex;
        
        &:last-child {
            margin-right: 0;
        }
    }

    .box {
        margin-top: 40px;

        &:first-child {
            margin-top: 0;
        }
    }

    .h-tit {
        min-height: 40px;
        margin-bottom: 16px;
        padding-bottom: 15px;
        border-bottom: 1px solid $plumcolor;
        font-size: 24px;
        font-weight: 800;

        @include box-sizing;
    }

    .items-list {
        > li {
            position: relative;
            min-height: 51px;
            padding: 7px 0;
            border-top: 1px solid #dfefee;
            font-size: 16px;

            @include flexbox;
            @include box-sizing;
            @include align-items(center);

            .tit {
                position: relative;
                width: 50%;
                padding-left: 20px;

                @include box-sizing;

                &:before {
                    content: '';
                    position: absolute;
                    left: 0;
                    top: 50%;
                    display: block;
                    background: #222;

                    @include size(4px);
                    @include border-radius(100px);
                    @include translate(0, -50%);
                }
            }

            .conts {
                color: $basecolor1;
                line-height: 1.2;

                em {
                    font-size: 14px;
                    color: $basecolor2;
                    white-space: nowrap;
                }
            }

            &.unset {
                @include flex-wrap(wrap);
            }

            &:first-child {
                border-top: 0;
            }
        }

        .list-sub {
            margin-top: 15px;
            padding-left: 20px;

            @include flex(1,1, 100%);
            @include box-sizing;

            li {
                position: relative;
                margin-top: 8px;
                font-size: 14px;
                color: $basecolor1;

                &:first-child {
                    margin-top: 0;
                }
            }

            .sub-price {
                position: absolute;
                right: 0;
                top: 50%;

                @include translate(0, -50%);
            }
        }

        // 가로형
        &.horizontal {
            @include clear;

            > li {
                float: left;
                width: calc((100% - 60px) / 2);
                margin-right: 60px;

                &.full {
                    float: none;
                    margin-right: 0;
                }

                &:first-child,
                &:first-child + li {
                    border-top-width: 0;
                }

                &:nth-child(even) {
                    margin-right: 0; 
                }
            }
        }

        // 양쪽정렬
        &.between {
            > li {
                @include justify-content(space-between);

                .conts {
                    text-align: right;
                }

                .lft {
                    text-align: left;
                }
            }
        }
    }
}

// 약관 체크
.cpn-terms-check {
    .terms-check {
        padding: 4px 25px;
        border: 1px solid #ededed;
        font-size: 20px;
        font-weight: 700;

        li {
            position: relative;
            padding: 20px 0;
            border-top: 1px solid #ededed;

            &:first-child {
                border-top: 0;
            }
        }

        .check-label {
            position: relative;
            display: inline-block;
            padding-left: 36px;
            line-height: 24px;
            vertical-align: top;

            &:before {
                content: "";
                position: absolute;
                left: 0;
                top: 50%;
                background: url($url-images + "/comm/bg_check.png") no-repeat 0 0;
                background-size: cover;

                @include size(24px);
                @include translate(0, -50%);
            }
        }

        .check {
            visibility: hidden;
            position: absolute;
            opacity: 0;
            z-index: -9999;

            &:checked ~ .check-label:before {
                background-image: url($url-images + "/comm/bg_check_on.png");
            }
        }

        .btn-terms-view {
            position: absolute;
            right: 0;
            top: 50%;
            border: 0;
            font-size: 16px;
            color: $plumcolor;
            background: none;
            text-decoration: underline;

            @include translate(0, -50%);
        }
    }
}

// 파일등록
.cpn-filebox {
    overflow: hidden;
    position: relative;
    display: inline-block;
    vertical-align: top;
    
    .ifile {
        opacity: 0;
        position: absolute;
        left: -25%;
        right: -25%;
        top: -25%;
        bottom: -25%;
        width: 150%;
        height: 150%;
        background: transparent;
        cursor: pointer;

        &::-webkit-file-upload-button {
            cursor: pointer;
        }
    }

    .btn-file {
        width: 100%;
        
        @include border-radius(0);
    }
}

// 로그인
.cpn-login {
    .login-top {
        max-width: 480px;
        margin: 0 auto;

        .box {
            margin-top: 20px;

            &:first-child {
                margin-top: 0;
            }
        }

        .istyle {
            border: 1px solid #ccc;
            font-size: 16px;
        }
    }

    .login-bot {
        margin-top: 60px;
        padding-top: 35px;
        border-top: 1px solid #ededed;
        text-align: center;

        .box {
            max-width: 480px;
            margin: 0 auto 35px;
            text-align: left;
