// tab
.tab-wrap {
    position: relative;

    @include flexbox;

    &:before {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 2px;
        font-size: 0;
        background: $plumcolor;
        line-height: 0;
        // z-index: -1;
    }

    .btn-tab {
        height: 80px;
        border-width: 2px;
        border-style: solid;
        border-color: #ccc #ccc $plumcolor;
        font-size: 24px;
        font-weight: 700;
        background: #fff;
        line-height: 1.2;

        @include flex;

        & + .btn-tab {
            margin-left: -2px;
        }

        &:hover {
            background: #f9f9f9;
        }

        &.active {
            position: relative;
            border-color: $plumcolor $plumcolor transparent;
            color: $plumcolor;

            &:hover {
                border-bottom-color: #fff;
                background: inherit;
            }
        }
    }

    &.tab2 {
        .btn-tab {
            max-width: calc(100% / 2);
        }
    }

    &.tab3 {
        .btn-tab {
            max-width: calc(100% / 3);
        }
    }

    &.tab4 {
        .btn-tab {
            max-width: calc(100% / 4);
        }
    }
}

// 댓글
