@charset "utf-8";

#footer {
    position: relative;
    padding-bottom: 250px;
    background: #232323;

    &:after {
        content: "";
        position: absolute;
        left: 50%;
        bottom: 112px;
        width: 32px;
        height: 40px;
        margin-left: -16px;
        background: url($url-images + "comm/ico_plum_white.png") no-repeat 0 0;
        background-size: cover;
    }

    .footer-wrap {
        @include default-width(1200px);
        
        // padding: 0;
    }

    .footer-menu {
        padding: 60px 0;
        font-size: 0;
        text-align: center;

        li {
            position: relative;
            display: inline-block;
            margin-left: 15px;
            padding-right: 16px;
            font-size: 16px;
            vertical-align: top;

            &:after {
                content: "";
                position: absolute;
                right: 0;
                top: 50%;
                width: 1px;
                height: 8px;
                margin-top: -4px;
                background: rgba(255,255,255,0.3);
            }

            &:last-child {
                &:after {
                    display: none;
                }
            }
        }

        a {
            color: #fff;
        }
    }

    .footer-info {
        padding: 60px 0;
        border-top: 1px solid rgba(255,255,255,0.1);

        @include clear;

        .footer-wrap {
            position: relative;

            @include clear;
        }
        
        .info-company {
            float: left;
            margin-right: 85px;
            font-size: 16px;
            color: #fff;
            line-height: 1.6;

            .company-name {
                display: block;
                font-size: 20px;
                line-height: 1;
            }

            .company-tit {
                display: block;
                margin-top: 20px;
            }

            .company-address {
                color: $basecolor2;
            }

            .lnk {
                color: $basecolor2;
                text-decoration: underline;
            }

            & + .info-company {
                margin-right: 0;
            }
        }

        .info-sns {
            position: absolute;
            right: 0;
            top: -10px;

            @include clear;

            li {
                float: left;
                margin-left: 20px;
                color: #fff;
                text-align: center;

                &:first-child {
                    margin-left: 0;
                }
            }

            a {
                display: block;
                border: 2px solid #fff;
                line-height: 28px;

                @include size(32px);
                @include box-sizing;
                @include border-radius(100px);

                i {
                    line-height: inherit;
                }
            }
        }
    }

    .footer-noti {
        padding-top: 60px;
        border-top: 1px solid rgba(255,255,255,0.1);
        color: #fff;
        text-align: center;

        .caution {
            font-size: 24px;
            line-height: 1.3;
        }

        .noti-info {
            margin-top: 30px;
            @include opacity(50);

            li {
                font-size: 16px;
                line-height: 1.6;
            }
        }
    }
}

// mobile 768 ~ 1200
@include respond-to(tablet) {
    #footer {
        .footer-info {
            .info-sns {
                right: 24px;
            }
        }
    }
}

// mobile 767
@include respond-to(mobiles) {
    #footer {
        padding-bottom: 190px;
    
        &:after {
            bottom: 50px;
        }
    
        .footer-menu {
            padding: 21px 0;
    
            li {
                height: 34px;
                font-size: 14px;
                line-height: 34px;

                &.nextline {
                    
                }
            }
        }
    
        .footer-info {
            padding: 30px 0 0;
            
            .info-company {
                float: none;
                margin-right: 0;
                font-size: 13px;
                color: #fff;
                line-height: 1.6;
    
                .company-name {
                    font-size: 20px;
                }
    
                .lnk {
                    font-size: 14px;
                }

                &:nth-child(2) {
                    margin-top: 40px;
                }
            }
    
            .info-sns {
                position: static;
                margin-top: 60px;
            }
        }
    
        .footer-noti {
            margin-top: 60px;
            padding-top: 0;
            border-top: 0;
            text-align: left;
    
            .caution {
                font-size: 16px;
                line-height: 1.3;
            }
    
            .noti-info {
                margin-top: 20px;

                li {
                    font-size: 14px;
                }
            }
        }
    }
}